.fg {
  position: relative;
  margin: 0;
  z-index: 2;
  textarea,
  input ,
  .nice-select{
    border: 1px solid #EEEEF3;
    padding: 0 1.25em;
    height: 4.0625em;
    width: 100%;
    font-size: 1em;
    background-color: #fff;
    color: $black;
    &::placeholder {
      color: #808284;
    }
    &:focus {
      border-color: $primary;
    }
    &._hide-placeholder{
      &~.fake-placeholder{
        opacity: 0;
      }
    }
  }
  textarea{
    height: 7.5625em;
    padding-top: 1.125em;
    padding-bottom: 1.125em;
    resize: none;
  }
  label{
    transition: all .2s;
    font-size: 1em;
    line-height: 157%;
    display: block;
    margin-bottom: .5em;
    color: #000;
    span{
      color: #FD1212;
    }
  }
  .nice-select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0.1875em;
    white-space: nowrap;
    height: 3.1875em;
    padding: 0 .9375em;
    &:focus{
      border: $border;
    }
    &:before{
      content: '';
      order: 3;
      flex-shrink: 0;
      width: .5em;
      height: .75em;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(100%);
    }
    .current{
      font-size: 1em;
      line-height: 157%;
    }
    .list{
      margin-top: -.25em;
      border-radius: 0;
      width: calc(100% + 2px);
      left: -1px;
      border: $border;
      background: #FFF;
      box-shadow: none;
      max-height: 9.7em;
      overflow-y: auto;
      z-index: 5;
      @include scroll;
    }
    .option{
      height: 3.1875em;
      font-size: 1em;
      line-height: 157%;
      padding: 0 .9375em;
      display: flex;
      align-items: center;
      color: #2B2B2B;
      border: none;
      border-bottom: 1px solid #F2F6F8;
      &:last-child{
        border-bottom: none;
      }
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: $primary;
        border-color: $primary;
        font-weight: 400;
        color: #fff;
      }
    }
  }
  ._error-text{
    display: none;
  }
  &._error{
    input,
    textarea{
      border-color: #FF1515;
      &::placeholder{
        color: #FF1515;
      }
    }
    ._error-text{
      display: block;
      margin-top: .19em;
      color: #FF1515;
      span{
        font-size: 0.8125em;
      }
    }
  }
}


.pass-toggle{
  position: absolute;
  bottom: 1.65em;
  right: 1.25em;
  width: 1.25em;
  height: .9375em;
  @include center;
  svg{
    width: 100%;
    height: auto;
  }
  &._active{
    g{
      opacity: 1;
    }
  }
}

.m-radio,
.file-input,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    color: #787878;
    span{
      font-size: 1em;
      line-height: 131%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      transition: all .100s;
      background-color: #fff;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
  &._gray{
    label{
      color: #6B6E72;
    }
  }
  &._error{
    input:not(:checked){
      &~label:before{
        border-color: #FF1515;
      }
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-size: .6875em auto;
      }
    }
  }
  label {
    padding-left: 2.1875em;
    min-height: 1.375em;
    color: #363839;
    &:before {
      border: 1px solid #E9E9E9;
      width: 1.3125em;
      height: 1.3125em;
      @include bgImgSize(0);
      background-image: url(../img/check.svg);
      transition: all .0s;
    }
  }
  &._gray{
    label{
      color: #787878;
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    color: #2C333D;
    padding-left: 2.25em;
    min-height: 1.375em;
    font-size: 1em;
    line-height: 137.5%;
    &:before{
      width: 1.3125em;
      height: 1.3125em;
      border: $border;
      border-radius: 50%;
      background-image: url(../img/radio2.svg);
      @include bgImgSize(0%);
    }
    span{
      display: block;
      line-height: 157%;
      color: #5B626C;
      margin-top: .125em;
    }
  }
}

.rating{
  display: flex;
  &__title{
    margin-right: .6875em;
  }
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field{
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: 1em;
    &:not(:last-child){
      margin-right: .18em;
    }
    svg{
      width: 100%;
      height: auto;
    }
    &:hover {
      cursor: pointer;
      path{
        fill: $primary;
      }
      &~.rating-input__item {
        path{
          fill: $primary;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  path{
    fill: $primary;
  }
}


@media screen and (max-width: 992px){
  .rating-input{
    &__item{
      margin-right: .19em;
      width: .8125em;
    }
  }
}
