.line-title{
  position: relative;
  padding-bottom: .9375em;
  &:before{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 2.5em;
    height: 0.3125em;
    background-color: $primary;
  }
}

.slider{
  position: relative;
  .swiper{
    &-nav{
      display: flex;
      position: absolute;
      right: 0;
      bottom: 100%;
      margin-bottom: 2.875em;
    }
  }
}

.hits-slider{
  margin-top: 2.5em;
  .swiper-nav{
    display: none;
  }
  &._grid-desktop{
    @media screen and (min-width: 993px){
      .swiper-wrapper{
        display: grid;
        grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
        gap: 1.25em;
        transform: unset !important;
      }
      .swiper-slide{
        width: 100%;
      }
    }
  }
}

.favourite-btn{
  border-radius: 0.25em;
  background: #F2F2F6;
  width: 2.375em;
  height: 2.375em;
  @include center;
  &:hover{
    background-color: rgba($primary,.3);
  }
  &._added{
    path{
      fill: $black;
      stroke: $black;
    }
  }
  svg{
    width: .875em;
    height: auto;
  }
}

.product-card{
  position: relative;
  border: 1px solid #F2F6F8;
  font-family: $font3;
  color: #202426;
  display: flex;
  flex-direction: column;
  &__img{
    @include imgCover(75.8%);
  }
  .favourite-btn{
    position: absolute;
    top: .94em;
    right: .94em;
  }
  &__content{
    padding: .5em 1.25em;
    @include grid100gap(.25em);
  }
  &__reviews{
    display: flex;
    align-items: center;
    color: #808080;
    font-family: $font3;
    .rating{
      margin-right: .32em;
    }
    .text14{
      line-height: 178%;
    }
  }
  &__articul{
    line-height: 200%;
    color: #5C6165;
  }
  &__nav{
    margin-top: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 1.25em;
    border-top: 1px solid #F2F6F8;
  }
  .cart-btn{
    margin-top: -1px;
    margin-right: -1px;
    width: 7.9375em;
    padding: 0 1em;
    height: 3.4375em;
    font-family: $font1;
    font-weight: 500;
    text-transform: uppercase;
    span{
      font-size: 1em;
    }
  }
}

.product-prices{
  display: flex;
  align-items: center;
  &__num1{
    text-decoration: line-through;
    color: #7A7A7A;
    margin-right: .31em;
  }
}

.cart-btn{
  ._view2{
    display: none;
  }
  &._added{
    background: $black;
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }
}

.slider{
  &__wrap{
    overflow: hidden;
  }
}

.more{
  margin-top: 1.25em;
  display: flex;
  justify-content: center;
  &__btn{
    font-family: $font2;
    display: flex;
    align-items: center;
    height: 5.25em;
    font-weight: 500;
    text-transform: uppercase;
    color: #2D3234;
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
    span{
      font-size: .9375em;
    }
    svg{
      width: 1.5625em;
      height: auto;
      flex-shrink: 0;
      margin-right: .44em;
    }
  }
}

.news{
  background: #F8F9F9;
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 3.75em;
    .m-btn{
      padding: 0 2.31em;
    }
  }
}

.news-slider{
  margin-top: 2.5em;
  position: relative;
  .swiper{
    &-nav{
      display: flex;
      position: absolute;
      right: 0;
      bottom: 100%;
      margin-bottom: 2.875em;
    }
    &-container{
      width: calc(100% + 1.25em);
    }
    &-slide{
      width: calc(100% / 3);
      padding-right: 1.25em;
    }
    &-button{
      &:not(:hover){
        border-color: #F8F9F9;
        background: #FFF;
      }
    }
  }
}

.news-card{
  font-family: $font3;
  position: relative;
  display: flex;
  flex-direction: column;
  &__badges{
    position: absolute;
    width: 100%;
    padding: 1.25em;
    display: flex;
    flex-wrap: wrap;
    top: 0;
    left: 0;
  }
  &__img{
    @include imgCover(53.8%);
  }
  &__content{
    margin-top: 1.25em;
    @include grid100gap(.5em);
  }
  &__title{
    font-weight: 500;
  }
  &__desc{
    color: #75797B;
  }
  &__date{
    color: #343738;
    line-height: 164%;
  }
  &__nav{
    margin-top: auto;
    padding-top: .5em;
    display: flex;
  }
  &__link{
    text-transform: uppercase;
    color: $primary;
    height: 2.25em;
    font-weight: 700;
    &:hover{
      color: $black;
      path{
        stroke: $black;
      }
    }
    span{
      font-size: 0.9375em;
    }
    svg{
      width: 1.5625em;
      height: auto;
      margin-left: .375em;
    }
  }
}

.m-badge{
  padding: 0 .38em;
  height: 1.6875em;
  display: flex;
  align-items: center;
  background-color: $primary;
  color: #fff;
  font-family: $font2;
  font-weight: 500;
  text-transform: uppercase;
  span{
    font-size: .75em;
  }
  &:hover{
    color: #fff;
    background: #F36600;
  }
}

.about{
  padding: 5em 0 6.25em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 34em) 30.75em;
    gap: 3.25em;
    max-width: 81.25em;
  }
  .text{
    margin-top: 1.875em;
  }
  &__nav{
    display: flex;
    margin-top: 1.81em;
    .m-btn{
      height: 4.875em;
      padding: 0 2.5em;
      span{
        font-size: 1.0625em;
      }
      img{
        margin-left: 2.25em;
      }
    }
  }
  &__img{
    padding-top: 1.06em;
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .slider{
    .swiper-nav{
      margin-bottom: 1.875em;
    }
  }

  .hits-slider{
    margin-top: 1.56em;
    position: relative;
    .swiper{
      &-container{
        width: calc(100% + .9375em);
      }
      &-slide{
        width: calc(100% / 3);
        padding-right: .9375em;
      }
      &-nav{
        display: flex;
      }
    }
  }

  .favourite-btn{
    width: 1.5em;
    height: 1.5em;
    svg{
      width: .625em;
    }
  }

  .product-card{
    .favourite-btn{
      top: .875em;
      right: .875em;
    }
    &__content{
      padding: .5em .9375em;
      gap: .375em;
    }
    &__reviews{
      .rating{
        margin-right: .12em;
      }
      .text14{
        font-size: .75em;
      }
    }
    &__title{
      font-size: 1.125em;
      line-height: 117%;
      min-height: 4em;
      display: flex;
      align-items: center;
      @include truncate(4);
    }
    &__articul{
      font-size: .8125em;
      line-height: 154%;
    }
    &__nav{
      display: block;
      padding: .625em 0 0;
    }
    .cart-btn{
      width: 100%;
      height: 2.625em;
      margin: .625em 0 0;
      span{
        font-size: .9375em;
      }
    }
    .product-prices{
      padding: 0 .9375em;
    }
  }

  .product-prices{
    &__num1{
      .text14{
        font-size: .8125em;
      }
    }
    &__num2{
      .text19{
        font-size: 1em;
        line-height: 144%;
      }
    }
  }

  .more{
    margin-top: 1.56em;
    &__btn{
      height: 2.75em;
    }
  }

  .news{
    &__nav{
      margin-top: 1.56em;
    }
  }

  .news-slider{
    margin-top: 1.56em;
    .swiper{
      &-slide{
        width: 50%;
      }
      &-nav{
        margin-bottom: 1.72em;
      }
    }
  }

  .news-card{
    &__link{
      span{
        font-size: .875em;
      }
    }
  }

  .about{
    padding: 3.125em 0;
    overflow: hidden;
    &__inner{
      grid-template-columns: 25.3125em 20.625em;
      gap: 1.56em;
    }
    .text{
      margin-top: 1.56em;
    }
    &__nav{
      margin-top: 1.25em;
      .m-btn{
        height: 3.875em;
        span{
          font-size: 1em;
        }
      }
    }
    &__img{
      padding-top: 4.625em;
    }
  }
}


@media screen and (max-width: 576px){
  ._hide-mob{
    display: none;
  }

  .hits-slider{
    .swiper-slide{
      width: 50%;
    }
  }

  .review-count{
    display: flex;
    align-items: center;
    &:not(._big){
      &:before{
        content: '';
        flex-shrink: 0;
        background-image: url(../img/comment.svg);
        @include bgImgSize(100%);
        margin-right: .12em;
        width: 0.6875em;
        height: 0.6875em;
      }
      span{
        display: none;
      }
    }
  }

  .product-card{
    .favourite-btn{
      top: .56em;
      right: .56em;
    }
    &__content{
      gap: .25em;
      padding: .5em .625em;
    }
    .rating{
      margin-right: .37em;
    }
    &__title{
      min-height: unset;
      -webkit-line-clamp: 3;
    }
    .product-prices{
      padding: 0 .625em;
    }
  }

  .news-slider{
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }

  .about{
    padding: 2.5em 0;
    &__inner{
      grid-template-columns: 100%;
    }
    &__nav{
      margin-top: 1.82em;
      .m-btn{
        padding: 0;
        width: 100%;
      }
    }
    &__img{
      padding-top: 0;
    }
  }
}
