.search{
  padding-bottom: 6.25em;
  &__inner{
    width: 61.5625em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__title{
    text-align: center;
  }
}

.search-form{
  border: .6875em solid $lightGray;
  padding: 1.5em;
  margin-top: 3.125em;
  @include grid100gap(1.56em);
  .empty-catalog{
    margin-top: 0;
  }
  &__title{
    text-align: center;
    color: $gray;
  }
  &__fields{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 3.125em / 3));
    gap: 1.5625em;
  }
  &__bottom{
    display: flex;
    justify-content: center;
    align-items: center;
    .m-btn{
      padding: 0 2.31em;
      img{
        margin-left: 1.56em;
      }
    }
  }
  &__checks{
    margin-left: 1.81em;
    @include grid100gap(.75em);
    label{
      color: #787878;
    }
  }
}

._full{
  grid-column: 1/-1;
}


@media screen and (max-width: 992px){
  .search{
    padding-bottom: 5em;
    .catalog-search{
      input{
        height: 3.75em;
      }
    }
  }

  ._full-tablet{
    grid-column: 1/-1;
  }

  .search-form{
    margin-top: 1.875em;
    border-width: .4375em;
    padding: .8125em;
    &__fields{
      grid-template-columns: repeat(2,calc(50% - 1.5625em / 2));
    }
    &__bottom{
      justify-content: unset;
      .m-btn{
        width: 18.125em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .search-form{
    &__fields{
      grid-template-columns: 100%;
    }
    &__bottom{
      display: block;
      .m-btn{
        width: 100%;
      }
    }
    &__checks{
      margin: 1.81em 0 0;
    }
  }
}
