.delivery{
  padding-bottom: 6.25em;
  &__groups{
    margin-top: 1.56em;
    @include grid100gap(2.25em);
  }
}

.delivery-group{
  h3{
    color: $black;
    @include lastM(0 0 .25em);
  }
  &__items{
    margin-top: .9375em;
    @include grid100gap(.9375em);
  }
}

.delivery-item{
  display: flex;
  &__icon{
    width: 2.4375em;
    flex-shrink: 0;
    margin-right: .9375em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    min-height: 2.4375em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    .text17{
      margin-top: .3em;
      color: $black3;
    }
  }
}

.guarantee{
  padding-bottom: 6.25em;
  .text{
    margin-top: 2.5em;
    font-weight: 400;
    color: $black3;
    hr{
      height: .5em;
    }
    blockquote{
      padding: 1.25em;
      background: #F8F9FA;
      border: none;
    }
  }
}

.conf{
  padding-bottom: 6.25em;
  .text{
    color: $black3;
    margin-top: 1.875em;
    font-weight: 400;
    a{
      color: $primary;
    }
  }
  &__hint{
    padding: 1.25em 1.56em;
    border-radius: 0.5em;
    background: #F8F8F8;
    margin-top: 2.5em;
    .text{
      margin: 0;
    }
  }
}


@media screen and (max-width: 992px){
  .delivery{
    padding-bottom: 5em;
    &__groups{
      gap: 1.75em;
    }
  }

  .guarantee{
    padding-bottom: 5em;
    .text{
      margin-top: 1.56em;
    }
  }

  .conf{
    padding-bottom: 5em;
    &__hint{
      margin-top: 1.875em;
    }
  }
}


@media screen and (max-width: 576px){
  .conf{
    .text58{
      font-size: 2em;
    }
  }
}
