.main{
  background-image: url(../img/main-bg.webp);
  background-size: cover;
  background-position: center;
  overflow: hidden;
  &__inner{
    padding-top: 7.87em;
    position: relative;
  }
  &__img{
    position: absolute;
    top: 8em;
    right: 0;
    width: 52.5em;
    height: 100%;
    display: flex;
    align-items: center;
    img{
      width: 100%;
      display: block;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  &__content{
    max-width: 32em;
  }
  &__title{
    text-transform: uppercase;
    span{
      color: $primary;
    }
  }
  &__text{
    font-family: $font3;
    margin-top: 1.94em;
    color: #54545A;
    max-width: 29em;
  }
  &__nav{
    z-index: 2;
    display: flex;
    margin-top: 8.38em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 4000px;
      height: 100%;
      background: #2A2F32;
    }
    .m-btn{
      position: relative;
      z-index: 2;
      min-width: 23em;
      height: 5.375em;
      padding: 0 2.81em;
      img{
        margin-left: 2.19em;
      }
      span{
        font-size: 1.0625em;
      }
      ._view2{
        display: none;
      }
    }
  }
}

.m-section{
  padding: 3.75em 0;
}

.equips{
  &__top{
    padding: .625em 0;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    gap: 1.25em;
    margin-top: 1.562em;
  }
}

.equip-card{
  text-transform: uppercase;
  font-weight: 500;
  border-radius: 0.25em;
  border: 1px solid #EBEEEE;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: .94em;
  &:hover{
    border-color: $primary;
  }
  &__img{
    margin-bottom: .94em;
    height: 6.875em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
}

.advantages{
  background-color: $black3;
  color: #fff;
  &__top{
    text-align: center;
    margin-bottom: 3.125em;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 8.24em / 3));
    gap: 4.56em 4.12em;
  }
}

.advantage-item{
  display: grid;
  grid-template-columns: 5em calc(100% - 6.25em);
  gap: 1.25em;
  &__icon{
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    padding-top: 1.25em;
  }
  &__title{
    text-transform: uppercase;
  }
  &__desc{
    margin-top: .625em;
    opacity: .8;
  }
}


@media screen and (max-width: 992px){
  .main{
    &__inner{
      padding-top: 4.31em;
    }
    &__content{
      max-width: 20.0625em;
    }
    &__text{
      margin-top: 1.12em;
    }
    &__nav{
      margin-top: 4.31em;
      margin-left: -1.5625em;
      .m-btn{
        min-width: 15.875em;
        height: 4.0625em;
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
        span{
          font-size: 1em;
        }
        img{
          margin-left: .625em;
        }
      }
    }
    &__img{
      width: 26.4375em;
      top: 0;
      right: -1.5625em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .m-section{
    padding: 3.125em 0;
  }

  .equips{
    &__grid{
      margin-top: .9375em;
    }
  }

  .equip-card{
    &__img{
      height: 3.875em;
      margin-bottom: .625em;
    }
    &__title{
      font-size: .9375em;
      line-height: 120%;
    }
  }

  .advantages{
    &__top{
      margin-bottom: 1.25em;
      padding: 0 3em;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
  }

  .advantage-item{
    grid-template-columns: 3.375em calc(100% - 3.375em - .9375em);
    gap: .9375em;
    &__title{
      font-size: 1em;
    }
    &__desc{
      font-size: .9375em;
      line-height: 127%;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__inner{
      padding-top: 2.44em;
    }
    &__text{
      max-width: 11.8125em;
    }
    &__nav{
      display: grid;
      grid-template-columns: 50% 50%;
      margin-top: 3.44em;
      margin-left: -.9375em;
      width: calc(100% + 1.875em);
      .m-btn{
        min-width: unset;
        padding: 0;
        width: 100%;
      }
    }
    &__img{
      width: 11.75em;
      right: -.9375em;
      top: unset;
      bottom: -3em;
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
  }

  .m-section{
    padding: 2.5em 0;
  }

  .equips{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .9375em / 2));
      gap: .9375em;
    }
  }

  .advantages{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
