.footer{
  background: $black;
  color: #FFF;
  &__title{
    font-family: $font1;
    font-size: 1.0625em;
    line-height: 118%;
    text-transform: uppercase;
    font-weight: 700;
  }
}

.footer-top{
  padding: 3.125em 0;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
}

.footer-contacts{
  &__items{
    margin-top: .87em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__phone,
  &__email{
    display: flex;
    align-items: center;
    margin-bottom: .06em;
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .44em;
    }
  }
  &__phone{
    font-weight: 500;
    span{
      font-size: 1em;
      line-height: 144%;
    }
  }
  &__whatsapp{
    padding: 0 .75em;
    text-transform: uppercase;
    background: #33383C;
    color: #fff;
    height: 2.0625em;
    font-weight: 500;
    font-family: $font3;
    margin-bottom: .06em;
    &:hover{
      background: $primary;
      color: #fff;
    }
    span{
      font-size: .8125em;
      line-height: 120%;
    }
  }
  &__email{
    color: $primary;
    &:hover{
      color: #fff;
    }
    span{
      transition: inherit;
      text-decoration: underline;
      font-size: .9375em;
      line-height: 125%;
    }
  }
}

.footer-nav{
  &__show svg{
    display: none;
  }
  &__hidden{
    margin-top: 1.125em;
    @media screen and (min-width: 577px){
      display: block !important;
    }
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    text-transform: uppercase;
    @include lastM(0 0 1.125em);
    span{
      font-size: .875em;
      line-height: 136%;
    }
  }
}

.footer-subscribe{
  align-self: flex-start;
  width: 23.125em;
  @include grid100gap(.75em);
  &__desc{
    font-size: .875em;
    line-height: 125%;
    color: #E3E3EA;
  }
  &__hint{
    color: #A2A6A8;
  }
}

.subscribe-form{
  &__grid{
    height: 2.9375em;
    display: grid;
    grid-template-columns: calc(100% - 8.75em) 8.75em;
  }
  .fg{
    height: 100%;
    &._error{
      input{
        border-color: #F32C00 !important;
        color: #F32C00;
        &::placeholder{
          color: #F32C00;
        }
      }
    }
    input{
      height: 100%;
      background-color: transparent;
      border-right: none;
      font-size: .8125em;
      color: #fff;
      font-weight: 500;
      font-family: $font3;
      &:not(:focus){
        border-color: rgba(216, 221, 228, 0.14);
      }
      &::placeholder{
        color: #C1C5C9;
      }
    }
  }
  .m-btn{
    padding: 0;
    height: 100%;
    text-transform: uppercase;
    font-weight: 700;
    span{
      font-size: .875em;
    }
  }
  &__hint{
    margin-top: .56em;
    color: #F35700;
    .text13{
      line-height: 120%;
    }
  }
  .m-check{
    margin-top: .56em;
    input {
      &:checked+label {
        &::before {
          background-color: transparent;
          border-color: rgba(216, 221, 228, 0.38);
          background-size: .6875em auto;
        }
      }
    }
    label{
      color: #909397;
      display: flex;
      align-items: center;
      min-height: 1em;
      padding-left: 1.625em;
      &:before{
        width: 1em;
        height: 1em;
        border-radius: 0.125em;
        border: 1px solid rgba(216, 221, 228, 0.38);
        background-color: transparent;
        background-image: url(../img/check2.svg);
      }
      span{
        font-size: 0.75em;
        line-height: 125%;
      }
    }
  }
}

.footer-bottom{
  padding: 1.5em 0;
  background: #1F2021;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__item{
    color: #979CA1;
    span{
      font-size: .9375em;
      line-height: 133%;
    }
  }
  &__conf{
    text-decoration: underline;
  }
}


@media screen and (max-width: 992px){
  .footer-top{
    padding: 1.875em 0;
    &__inner{
      flex-wrap: wrap;
    }
  }

  .footer-contacts{
    width: 100%;
    margin-bottom: 2em;
    &__items{
      margin-top: .875em;
      display: grid;
      grid-template-columns: 1fr 1fr 1.1fr .9fr;
      gap: .81em;
      align-items: center;
    }
    &__phone,
    &__email,
    &__whatsapp{
      margin: 0;
    }
    &__whatsapp{
      max-width: 20.5em;
      grid-column: span 2;
    }
  }

  .footer-nav{
    max-width: 30%;
  }

  .footer-subscribe{
    margin-top: 2.19em;
    width: 100%;
  }

  .subscribe-form{
    &__grid{
      grid-template-columns: calc(100% - 17em) 17em;
    }
  }

  .footer-bottom{
    padding: .94em 0;
    &__item{
      span{
        font-size: .875em;
        line-height: 136%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .footer-contacts{
    margin-bottom: 1.25em;
    &__items{
      grid-template-columns: 1.1fr 1fr;
    }
    &__whatsapp{
      max-width: 100%;
    }
    &__email{
      white-space: nowrap;
    }
  }

  .footer-nav{
    width: 100%;
    max-width: 100%;
    &._active{
      .footer-nav__show svg{
        transform: rotate(-180deg);
      }
    }
    &__show{
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: .69em .25em .69em 0;
      border-bottom: 1px solid #333435;
      svg{
        transition: all .3s;
        display: block;
        width: .5em;
        height: auto;
      }
    }
    &__hidden{
      padding-bottom: .5em;
      display: none;
    }
  }

  .footer-subscribe{
    margin-top: 1.25em;
  }

  .subscribe-form{
    &__grid{
      grid-template-columns: calc(100% - 7.8em) 7.8em;
    }
  }

  .footer-bottom{
    text-align: center;
    &__inner{
      flex-direction: column;
    }
    &__item{
      @include lastM(0 0 .31em);
    }
  }
}
