.categories-page{
  padding-bottom: 6.25em;
}

.brands{
  margin-top: 1.69em;
  &__grid{
    margin-top: .9375em;
    display: grid;
    grid-template-columns: repeat(4,25%);
    width: calc(100% + 3px);
    @media screen and (min-width: 577px){
      .brand-card{
        margin: -1px -1px 0 0;
        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(3),
        &:nth-child(4){
          margin-top: 0;
        }
        &:nth-child(4n-3){
          margin-left: 0;
        }
      }
    }
  }
}

.brand-card{
  border: 1px solid #EEEEF1;
  min-height: 9.0625em;
  text-transform: uppercase;
  position: relative;
  @include center;
  &:hover{
    border-color: $primary;
    z-index: 2;
  }
  &__img{
    height: 4.68em;
    margin-right: 2em;
    img{
      max-height: 100%;
      max-width: 100%;
    }
  }
  &__content{
    @include grid100gap(.5em);
  }
  &__title{
    font-family: $font1;
    font-weight: 500;
  }
  &__desc{
    color: #88888C;
    line-height: 120%;
  }
}

.categories{
  margin-top: 3.75em;
  &__grid{
    margin-top: .9375em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    gap: 1.25em;
  }
}

.category-card{
  border: 1px solid #EEEEF1;
  display: flex;
  flex-direction: column;
  &__img{
    display: block;
    @include center;
    height: 11.3125em;
    margin-bottom: 1.94em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    padding: 0 .25em;
    text-align: center;
    text-transform: uppercase;
    font-weight: 500;
    line-height: 140%;
    ._view2{
      display: none;
    }
  }
  &__nav{
    padding-top: .6em;
    margin-top: auto;
  }
  &__link{
    width: calc(100% + 2px);
    margin: 0 0 -1px -1px;
    height: 3.125em;
    border: 1px solid #EEEEF1;
    font-weight: 700;
    text-transform: uppercase;
    color: $primary;
    &:hover{
      background-color: $primary;
      color: #fff;
      border-color: $primary;
      path{
        stroke: #fff;
      }
    }
    span{
      font-size: .9375em;
      line-height: 153%;
    }
    svg{
      width: 1.5625em;
      height: auto;
      margin-left: .38em;
    }
  }
}


@media screen and (max-width: 992px){
  .categories-page{
    padding-bottom: 5em;
  }

  .brands{
    margin-top: 2.5em;
  }

  .brand-card{
    flex-direction: column;
    justify-content: unset;
    padding: 1.5em .5em .93em;
    text-align: center;
    &__img{
      justify-content: center;
      height: 3.5625em;
      margin: 0 0 .53em;
    }
    &__content{
      gap: .12em;
    }
    &__title{
      font-size: 1.0625em;
    }
  }

  .categories{
    margin-top: 2.5em;
    &__grid{
      margin-top: .625em;
      grid-template-columns: repeat(4,calc(25% - .9375em * 3 / 4));
      gap: .9375em;
    }
  }

  .category-card{
    &__img{
      margin-bottom: 1em;
      height: 6.45em;
    }
    &__title{
      font-size: 1em;
      line-height: 112%;
      ._view1{
        display: none;
      }
      ._view2{
        display: inline;
      }
    }
    &__link{
      height: 2.5em;
      span{
        font-size: .8125em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .brands{
    &__grid{
      grid-template-columns: repeat(2,50%);
      width: calc(100% + 1px);
      .brand-card{
        margin: -1px -1px 0 0;
        &:nth-child(1),
        &:nth-child(2){
          margin-top: 0;
        }
        &:nth-child(2n-1){
          margin-left: 0;
        }
      }
    }
  }

  .categories{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .9375em / 2));
    }
  }
}
