.catalog-detail{
  margin-top: 2.19em;
  .tab-content{
    margin-top: 2.5em;
  }
  .text{
    color: $black;
  }
}

.tabs{
  display: flex;
  border-bottom: 1px solid #F3F3F8;
}

.tab{
  height: 3.6875em;
  padding: 0 2.06em;
  display: flex;
  align-items: center;
  position: relative;
  text-transform: uppercase;
  font-weight: 500;
  font-family: $font1;
  color: rgba($black,.8);
  transition: all .3s;
  &:before{
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    bottom: 0;
    background-color: $primary;
    opacity: 0;
    transition: inherit;
  }
  span{
    font-size: 1em;
    line-height: 156%;
  }
  &:hover{
    background: #F3F3F8;
    cursor: pointer;
  }
  &._active{
    background: #F3F3F8;
    pointer-events: none;
    &:before{
      opacity: 1;
    }
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.catalog-desc{
  max-width: 68.5625em;
  .text{
    img{
      border: 1px solid #F2F6F8;
      float: left;
      width: 33.625em;
      margin-right: 1.56em;
      margin-bottom: .94em;
    }
  }
}

.catalog-delivery{
  max-width: 68.5625em;
}

.reviews{
  display: grid;
  grid-template-columns: calc(100% - 33.625em - 1.25em) 33.625em;
  gap: .9375em 1.25em;
  &__items{
    margin-top: 1.875em;
    @include grid100gap(1.25em);
  }
}

.review-card{
  border: 1px solid #EFEFEF;
  padding: 1.56em;
  @include grid100gap(.375em);
  &__desc{
    color: #363636;
  }
  &__date{
    color: $black3;
    font-size: .8125em;
    line-height: 192%;
    letter-spacing: 0.01em;
  }
}

.review-form{
  position: sticky;
  top: 1em;
  padding: 2.5em;
  background: #FAFAFA;
  &__title{
    text-align: center;
  }
  .rating{
    margin-top: .9375em;
    justify-content: center;
  }
  &__fields{
    margin-top: 1.56em;
    @include grid100gap(1.25em);
  }
  &__nav{
    margin-top: 1.56em;
    display: flex;
    justify-content: center;
    .m-btn{
      height: 4.0625em;
      padding: 0 2.81em;
      font-weight: 500;
      text-transform: uppercase;
      span{
        font-size: 1.0625em;
      }
    }
  }
  .thank{
    max-width: 24.5em;
  }
}


@media screen and (max-width: 992px){
  .catalog-detail{
    .tab-content{
      margin-top: 1.56em;
    }
  }

  .tab{
    padding: 0 1.25em;
    height: 2.6875em;
  }

  .catalog-desc{
    .text{
      img{
        width: 20.4375em;
      }
      p{
        font-size: 1.0625em;
      }
    }
  }

  .reviews{
    grid-template-columns: 100%;
    &__items{
      gap: .9375em;
      margin-top: .9375em;
    }
  }

  .review-form{
    padding: 1.25em;
    &__fields{
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
    &__nav{
      .m-btn{
        width: 100%;
        height: 3.3125em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .tabs{
    width: calc(100% + 1.875em);
    margin-left: -.9375em;
    padding: 0 .9375em .5em;
    border-bottom: none;
    overflow-x: auto;
  }

  .tab{
    white-space: nowrap;
    flex-shrink: 0;
    border-bottom: 1px solid #F3F3F8;
  }

  .catalog-desc{
    .text{
      img{
        width: 100%;
        margin-right: 0;
        margin-bottom: 1.56em;
      }
    }
  }

  .review-form{
    &__fields{
      grid-template-columns: 100%;
    }
  }
}
