.cookie-modal{
    position: fixed;
    bottom: 0;
    left: 0;
    background: $black;
    color: #FFF;
    z-index: 20;
    padding: 1.56em 0;
    width: 100%;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &._active {
        opacity: 1;
        pointer-events: unset;
    }
    &__inner{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__content{
        @include grid100gap(.5em);
        max-width: 57.5em;
    }
    &__title{
        line-height: 132%;
        font-weight: 500;
    }
    &__desc{
        font-size: 1.0625em;
        line-height: 147%;
        a{
            text-decoration: underline;
        }
    }
    &__nav{
        display: flex;
    }
    &__btn{
        max-width: 100%;
        padding: 0 2.75em;
        height: 3.0625em;
    }
}

.modal{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg {
        background: rgba(37, 38, 39, 0.90);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }
    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
}

.modal {
    overflow-y: auto;
    padding: 2em 0;
    text-align: center;
    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog{
        text-align: left;
        vertical-align: middle;
    }
    &__dialog {
        background: #fff;
        width: 37.75em;
        max-width: calc(100% - 1.875em);
        position: relative;
        z-index: 3;
        padding: 2.19em 3.22em;
        display: inline-grid;
        grid-template-columns: 100%;
        gap: 1.25em;
        border-radius: 0.1875em;
    }
    &__close {
        z-index: 10;
        border-top: 0;
        background-color: transparent;
        width: 1em;
        @include center;
        position: absolute;
        left: 100%;
        bottom: 100%;
        margin: 0 0 .25em .25em;
        &:hover{
            path{
                opacity: 1;
            }
        }
        svg {
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                opacity: 1;
            }
        }
    }
    &__top{
        color: #17212C;
        text-align: center;
        @include grid100gap(1.25em);
    }
    &__text{
        letter-spacing: 0.02em;
        line-height: 206%;
        color: #534E54;
    }
}

.modal-form{
    @include grid100gap(1.56em);
    .m-check{
        label{
            color: #78736F;
        }
    }
    &__nav{
        display: flex;
        justify-content: center;
    }
    &__checks{
        @include grid100gap(.75em);
    }
}


@media screen and (max-width: 992px){
    .cookie-modal{
        padding: 1.25em 0;
        &__inner{
            display: block;
        }
        &__nav{
            display: flex;
            margin-top: .5em;
        }
    }

    .modal{
        padding: 5em 0;
        &__dialog{
            gap: 1.25em;
            padding: 1.25em;
            width: 28.875em;
        }
        &__close{
            position: absolute;
            top: unset;
            bottom: 100%;
            width: .875em;
            margin: 0 -.625em .875em 0;
        }
        &__top{
            gap: .75em;
        }
        &__nav{
            .m-btn{
                width: 100%;
            }
        }
        .m-check{
            label{
                span{
                    font-size: .8125em;
                    line-height: 130%;
                }
            }
        }
    }

    .modal-form{
        gap: 1.25em;
        &__nav{
            .m-btn{
                width: 100%;
            }
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog{
            padding-left: .9375em;
            padding-right: .9375em;
        }
        &__close{
            margin-left: -.75em;
        }
    }
}
