.contacts{
  &__content{
    margin-top: 2.5em;
    border-top: $border;
    border-bottom: $border;
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 35.6875em) 35.6875em;
  }
  &__top{
    display: grid;
    grid-template-columns: calc(100% - 24.8125em) 24.8125em;
  }
}

.contacts-info{
  padding: 1.56em 1.56em 1.56em 0;
  &__items{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: .75em;
  }
  &__item{
    display: flex;
    @include lastM(0 0 .75em)
  }
  &__icon{
    width: 1.5em;
    flex-shrink: 0;
    margin-right: .31em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__email{
    color: $primary;
    text-decoration: underline;
  }
  &__desc{
    .text17{
      line-height: 117%;
      color: $black3;
    }
  }
}

.contacts-review{
  border-left: $border;
  padding: 1.56em;
  &__link{
    @include center;
    margin-top: 1.25em;
    height: 3.1875em;
    border: 2px solid #E9E9F2;
    border-radius: 0.25em;
    img{
      max-height: 1.3125em;
      max-width: 7.375em;
    }
  }
}

.managers{
  padding: 1.56em 0 1.56em 1.56em;
  border-left: $border;
  min-height: 100%;
  &__grid{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.875em));
    gap: 2.8em 3.75em;
  }
}

.avatar{
  width: 3.25em;
  height: 3.25em;
  border-radius: 50%;
  flex-shrink: 0;
  img{
    width: 100%;
    height: 100%;
    display: block;
    border-radius: inherit;
    object-fit: cover;
  }
}

.manager-card{
  display: flex;
  .avatar{
    margin-right: .9375em;
  }
  &__content{
    .text20{
      color: $black2;
    }
  }
  &__prof{
    color: #61676A;
    line-height: 175%;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: .41em;
  }
  .header-phone__link,
  .header-contacts__email{
    margin-top: 0;
    @include lastM(0 0 .5em);
  }
}

.map{
  height: 31.0625em;
  width: calc(100% + 5em);
  margin-left: -5em;
  @media screen and (min-width: 1441px){
    width: calc(100% + ((100vw - 1440px + 5em) / 2));
    margin-left: calc((100vw - 1440px + 5em) / 2 * -1);
  }
}

.contacts-feedback{
  padding: 5em 0;
  background: #FAFAFA;
  &__title{
    text-align: center;
    padding: 0 1em;
  }
}

.contact-form{
  margin: 2.5em auto 0;
  max-width: 47.25em;
  .search-form__fields{
    margin: 0;
    @media screen and (min-width: 577px){
      grid-template-columns: repeat(2,calc(50% - 1.5625em / 2));
    }
  }
  .search-form__bottom{
    margin-top: 1.56em;
  }
}


@media screen and (max-width: 992px){
  .contacts{
    &__content{
      margin-top: 1.875em;
    }
    &__grid{
      grid-template-columns: 100%;
      position: relative;
      padding-bottom: 31.0625em;
    }
    &__top{
      grid-template-columns: 50% 50%;
    }
    .map{
      position: absolute;
      bottom: 0;
      left: 0;
      margin: 0;
      width: 100%;
    }
  }

  .managers{
    padding: 1.56em 6.5em;
    border-left: none;
    border-top: $border;
    .text23{
      text-align: center;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: .9375em 1.25em;
    }
  }

  .contacts-feedback{
    padding: 2.5em 0;
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__top{
      grid-template-columns: 100%;
    }
  }

  .contacts-info{
    padding-right: 0;
  }

  .contacts-review{
    width: calc(100% + 1.875em);
    margin-left: -.9375em;
    padding: 1.56em;
    border-left: none;
    border-top: $border;
  }

  .managers{
    padding: 1.56em 0;
    .text23{
      text-align: left;
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
