.page-top{
    padding: .9375em 0;
    border-top: 1px solid #EEEEF1;
    border-bottom: 1px solid #EEEEF1;
    margin-bottom: 2.5em;
}

.woocommerce-breadcrumb{
    text-transform: uppercase;
    color: $black3;
    font-family: $font1;
    a,
    span{
        font-size: 0.8125em;
        line-height: 123%;
        position: relative;
        &:not(:first-child) {
            margin-left: 3em;
            &:before {
                content: '';
                margin-right: 1em;
                height: 1.23em;
                display: flex;
                align-items: center;
                position: absolute;
                z-index: 1;
                top: -.125em;
                right: 100%;
            }
        }
    }
    span{
        opacity: 0.7;
    }
}


@media screen and (max-width: 992px){
    .page-top{
        margin-bottom: 1.875em;
    }

    .woocommerce-breadcrumb{
        a,span{
            font-size: .75em;
            &:not(:first-child) {
                margin-left: 2em;
                &:before {
                    margin-right: .6875em;
                    height: 1.23em;
                }
            }
        }
    }
}
