.catalog-item{
  padding-bottom: 2.125em;
  &__top{
    display: grid;
    grid-template-columns: calc(100% - 37.875em - 2.19em) 37.875em;
    gap: 2.19em;
  }
}

.gallery{
  display: grid;
  grid-template-columns: 5.75em calc(100% - 7em);
  gap: 1.25em;
  &__img{
    display: block;
    @include imgCover(76.2%);
    padding: 0;
    height: 100%;
    transition: all .3s;
    img{
      transition: all .3s;
    }
  }
  &__small{
    position: relative;
    height: 26.5625em;
    .swiper{
      height: calc(100% + 1.25em);
      &-slide{
        height: 20%;
        padding-bottom: 1.25em;
        position: relative;
        &:hover{
          cursor: pointer;
        }
        &-thumb-active .gallery__img{
          border-color: $black;
          img{
            opacity: .5;
          }
        }
      }
    }
    .gallery__img{
      padding: 0;
      height: 100%;
      border: 1px solid #F2F6F8;
      &:hover{
        border-color: $black;
      }
    }
  }
  &__big{
    width: 100%;
    height: 26.5625em;
    position: relative;
    border: 1px solid #F2F6F8;
    .swiper{
      height: 100%;
      &-slide{
        padding: 3.25em 4.125em;
      }
    }
  }
  &__nav{
    display: none;
  }
  .favourite-btn{
    position: absolute;
    top: 1.25em;
    left: 1.25em;
    z-index: 3;
  }
}

.catalog-info{
  .product-card__reviews{
    margin-top: .31em;
  }
  .product-prices{
    margin-top: 1em;
    &__num1{
      margin-right: .81em;
    }
  }
  &__nav{
    display: flex;
    margin-top: 1em;
    .cartcalc{
      margin-right: 1.44em;
    }
  }
  &__btn{
    height: 4.0625em;
    width: 14.3125em;
    text-transform: uppercase;
    font-weight: 500;
    span{
      font-size: 1.0625em;
      line-height: 112%;
    }
  }
  .qa-card{
    margin-top: .625em;
  }
}

.cartcalc{
  width: 8.1875rem;
  display: grid;
  grid-template-columns: 2.1875em calc(100% - 2.1875em * 2) 2.1875em;
  border: 1px solid #ECEFF4;
  height: 4.0625em;
  button,
  input{
    height: 100%;
    width: 100%;
    min-height: 100%;
  }
  input{
    border: none;
    border-left: 1px solid #ECEFF4;
    border-right: 1px solid #ECEFF4;
    color: $black2;
    font-weight: 500;
    font-size: 1em;
    text-align: center;
  }
  svg{
    width: .5em;
    height: auto;
  }
  button{
    @include center;
    &:hover{
      background-color: rgba($primary,.2);
      path{
        fill: $primary;
      }
    }
  }
}

.catalog-auth-form{
  margin-top: 1em;
  border: .6875em solid $lightGray;
  padding: 1.5em;
  &__inner{
    @include grid100gap(1.56em);
  }
  .empty-catalog{
    margin: 0;
    color: #455358;
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 13.25em) 12.125em;
    gap: 1.125em;
    align-items: flex-end;
    .m-btn{
      height: 4.0625em;
    }
  }
}

.m-form{
  position: relative;
  &._success{
    .m-form__inner{
      opacity: 0;
      pointer-events: none;
    }
    .thank{
      opacity: 1;
      pointer-events: unset;
    }
  }
  .thank{
    opacity: 0;
    pointer-events: none;
    position: absolute;
    @include centerAbsolute;
    width: 100%;
    z-index: 2;
  }
}

.thank{
  padding: 2em;
  text-align: center;
  @include grid100gap(.9375em);
  &__icon{
    margin: 0 auto;
    width: 2.6875em;
    height: 2.6875em;
    background-color: $primary;
    background-image: url(../img/check3.svg);
    @include bgImgSize(1.5em);
  }
  &__title{
    text-transform: uppercase;
    font-family: $font1;
    font-weight: 700;
  }
}

.qa-card{
  &._active{
    ._hide{
      opacity: 0;
    }
  }
  &__show{
    padding: .44em 0;
    min-height: 2.5625em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #F5F5F5;
    &:hover{
      cursor: pointer;
      color: $primary;
    }
  }
  &__title{
    font-size: 1.125em;
    line-height: 150%;
    font-family: $font1;
    font-weight: 500;
    text-transform: uppercase;
  }
  &__btn{
    width: 1.5625em;
    flex-shrink: 0;
    margin-left: 1em;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  &__hidden{
    display: none;
    margin-top: .625em;
  }
  &__text{
    font-size: 1em;
    line-height: 156%;
    font-weight: 300;
  }
  &__nav{
    display: flex;
    margin-top: .625em;
  }
  &__link{
    font-size: .9375em;
    line-height: 167%;
    font-family: $font2;
    color: $primary;
    font-weight: 500;
    border-bottom: 1px dashed $primary;
    &:hover{
      color: $black;
      border-color: $black;
    }
  }
}

.params{
  padding: .9375em 1.25em;
  border-radius: 0.25em;
  border: 1px solid $lightGray;
  margin-top: 1em;
  @include grid100gap(.375em);
  .text18{
    text-transform: uppercase;
  }
  &__item{
    font-size: 1.0625em;
    line-height: 147%;
    b{
      font-weight: 700;
    }
  }
}

.product-collection{
  padding: 2.25em 0 6.25em;
}

.product-slider{
  margin-top: 2.5em;
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-slide{
      width: 25%;
      padding-right: 1.25em;
    }
  }
}

.product-detail{
  margin-top: 1em;
  border-radius: 0.25em;
  border: 1px solid #E7E7E7;
  max-width: 23.9375em;
  &__item{
    &:not(:last-child){
      border-bottom: 1px solid #E7E7E7;
    }
    &._active{
      .product-detail__btn{
        transform: rotate(90deg);
      }
    }
  }
  &__show{
    padding: .9375em 1.25em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &._toggle-product-detail{
      cursor: pointer;
      &:hover{
        color: $primary;
      }
    }
  }
  &__title{
    font-size: 0.9375em;
    line-height: 135%;
    font-weight: 500;
    a{
      color: #8C8C97;
      text-decoration: underline;
    }
    ._view2{
      display: none;
    }
  }
  &__btn{
    margin-left: .1em;
    background: #F2F2F6;
    width: 1.125em;
    height: 1.125em;
    flex-shrink: 0;
    border-radius: 50%;
    @include center;
    img{
      width: .3125em;
    }
  }
  &__hidden{
    padding: 0 1.25em .9375em;
    display: none;
  }
  &__links{
    display: flex;
    flex-wrap: wrap;
    margin-top: -.625em;
  }
  &__link{
    text-decoration-line: underline;
    color: $black3;
    margin: .625em .625em 0 0;
    span{
      font-size: 0.9375em;
      line-height: 133%;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    padding-bottom: 1.5625em;
    &__top{
      grid-template-columns: 20.625em calc(100% - 21.625em);
      gap: 1em;
    }
  }

  .gallery{
    grid-template-columns: 100%;
    gap: 1em;
    &__big{
      height: 15.4375em;
      .swiper-slide{
        padding: 1.875em;
      }
    }
    &__small{
      order: 2;
      height: auto;
      .swiper{
        height: auto;
        width: calc(100% + .9375em);
        &-slide{
          padding: 0 .9375em 0 0;
          height: 4.6875em;
          width: calc(100% / 3);
        }
      }
    }
    .favourite-btn{
      top: .9375em;
      left: .9375em;
      width: 2.375em;
      height: 2.375em;
      svg{
        width: .875em;
      }
    }
    &__nav{
      display: flex;
      align-items: center;
      padding: .75em 1em;
      right: 0;
      bottom: 0;
      position: absolute;
    }
    .swiper-button{
      border: none;
      background-color: transparent !important;
      width: .4375em;
      height: auto;
      margin-left: 1.25em;
      position: static;
      svg{
        width: 100%;
      }
      &:hover{
        path{
          fill: $primary;
        }
      }
    }
  }

  .catalog-info{
    .product-prices,
    &__nav{
      margin-top: .625em;
      .cartcalc{
        margin-right: .94em;
      }
    }
    &__btn{
      width: auto;
      flex-grow: 1;
      height: 3.3125em;
    }
  }

  .cartcalc{
    height: 3.3125em;
    width: 6.9375em;
  }

  .catalog-auth-form{
    margin-top: .625em;
    border-width: .4375em;
    padding: .5em;
    &__inner{
      gap: 1.25em;
    }
    &__grid{
      grid-template-columns: 100%;
      .m-btn{
        height: 3.375em;
      }
    }
  }

  .product-collection{
    padding: 1.5625em 0 5em;
    .text41{
      max-width: calc(100% - 4em);
    }
  }

  .product-slider{
    margin-top: 1.56em;
    .swiper{
      &-container{
        width: calc(100% + .9375em);
      }
      &-slide{
        width: calc(100% / 3);
        padding-right: .9375em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    &__top{
      grid-template-columns: 100%;
    }
  }

  .gallery{
    &__small{
      display: none;
    }
  }

  .catalog-auth-form{
    margin-top: 1em;
  }

  .news,
  .product-collection{
    &._small-title{
      .text41{
        max-width: calc(100% - 5.5em);
      }
      .swiper-nav{
        margin-bottom: 4em;
      }
    }
  }

  .product-slider{
    .swiper-slide{
      width: 50%;
    }
  }
}
