.text{
  color: #4D4D4D;
  font-weight: 300;
  blockquote,
  li,
  p{
    font-size: 1.0625em;
    line-height: 159%;
  }
  blockquote,
  p{
    @include lastM(0 0 1em);
  }
  b{
    font-weight: 700;
  }
  h3{
    font-size: 2.25em;
    line-height: 120%;
    font-weight: 500;
    margin-bottom: .5em;
  }
  a{
    text-decoration: underline;
  }
  ul{
    @include grid100gap(.56em);
    @include lastM(0 0 1em);
    li{
      position: relative;
      padding-left: 1.176em;
      &:before{
        content: '';
        position: absolute;
        left: 0;
        top: .353em;
        width: .764em;
        height: .764em;
        background-image: url(../img/list-marker.svg);
        @include bgImgSize(100%);
      }
    }
  }
  hr{
    height: 1em;
    display: block;
    opacity: 0;
  }
  img{
    width: 100%;
    display: block;
    @include lastM(0 0 1em);
  }
  ol{
    @include grid100gap(.75em);
    @include lastM(0 0 1em);
    counter-reset: section;
    li{
      counter-increment: section;
      position: relative;
      padding-left: 2.05em;
      line-height: 147%;
      &:before{
        content: counter(section);
        position: absolute;
        font-size: .76em;
        top: 0;
        left: 0;
        background-color: $primary;
        border-radius: 50%;
        color: #fff;
        font-weight: 700;
        font-family: $font1;
        width: 1.923em;
        height: 1.923em;
        @include center;
      }
    }
  }
  blockquote{
    border: .5em solid $lightGray;
    padding: 1em;
  }
}


@media screen and (max-width: 992px){
  .text{
    &:not(._not-small){
      p{
        font-size: .9375em;
        line-height: 153%;
      }
    }
    h3{
      font-size: 1.625em;
    }
  }
}
