.profile-form{
  margin-top: 1.56em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .78em));
  gap: 1.56em;
  &__nav{
    display: flex;
    .m-btn{
      padding: 0 2.31em;
      img{
        margin-left: .9375em;
      }
    }
  }
}

.orders-history{
  .order-table,
  .empty-catalog{
    margin-top: 1.56em;
  }
  .empty-catalog{
    justify-content: unset;
  }
  .pagination{
    @media screen and (min-width: 577px){
      &__items{
        justify-content: unset;
      }
    }
  }
}

.orders-item{
  @include grid100gap(2.31em);
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .cart-info{
    margin-top: -1em;
  }
  .address-card{
    padding: 1.625em;
  }
}

.back-link{
  display: flex;
  align-items: center;
  font-weight: 500;
  color: $black3;
  white-space: nowrap;
  svg{
    width: 1.5em;
    height: auto;
    margin-right: .625em;
  }
  span{
    font-size: 0.9375em;
    line-height: 180%;
  }
  ._view2{
    display: none;
  }
  &:hover{
    color: $primary;
    path{
      stroke: $primary;
    }
  }
}

.addresses{
  @include grid100gap(1.56em);
  &__items{
    @include grid100gap(1.56em);
  }
}

.address-card{
  border-radius: 0.1875em;
  border: .5em solid $lightGray;
  padding: .75em 1.6875em;
  @include grid100gap(1.06em);
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  &__btn{
    @include lastM(0 .625em 0 0);
    @include center;
    width: 2.8125em;
    height: 2.8125em;
    border-radius: 50%;
    &:hover{
      background-color: $lightGray;
    }
    img{
      width: 100%;
    }
  }
  &__bottom{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__street{
    font-size: 1em;
    line-height: 138%;
    max-width: 18em;
  }
}

.address-contact{
  @include grid100gap(.25em);
  &__title{
    font-size: 1.0625em;
    line-height: 118%;
    color: #87878D;
    font-weight: 500;
  }
  &__link{
    font-size: 1.125em;
    line-height: 111%;
    color: #3B3B3E;
    font-weight: 500;
    white-space: nowrap;
    display: inline-flex;
    margin-right: auto;
  }
}

.address-new{
  border-radius: 0.1875em;
  border: .5em solid $lightGray;
  padding: 1.6875em;
  @include grid100gap(.9375em);
  &__nav{
    display: flex;
  }
  &__btn{
    display: flex;
    align-items: center;
    font-weight: 500;
    color: $primary;
    &:hover{
      .address-new__btn-icon{
        background-color: $primary;
        path{
          stroke: #fff;
        }
      }
    }
    &-icon{
      transition: inherit;
      width: 3.875em;
      height: 3.875em;
      border-radius: 50%;
      @include center;
      flex-shrink: 0;
      margin-right: .9375em;
      background-color: $lightGray;
      svg{
        width: 1em;
        height: auto;
      }
    }
    &-title{
      font-size: 1em;
      line-height: 144%;
    }
  }
}

.pass-form{
  margin-top: 1.56em;
  max-width: 26.5625em;
  width: 100%;
  @include grid100gap(1.125em);
  .m-btn{
    margin-top: .4375em;
    width: 100%;
  }
}


@media screen and (max-width: 992px){
  .orders-item{
    gap: 1.875em;
    .cart-info{
      margin: 0;
    }
    .address-card{
      padding: 1.0625em;
    }
  }

  .address-card{
    padding: 1.0625em;
    gap: .9375em;
    &__street{
      max-width: 13.3125em;
    }
  }

  .address-new{
    padding: 1.0625em;
  }

  .pass-form{
    max-width: 100%;
  }

  .profile-form{
    &__nav{
      &._full-tablet{
        .m-btn{
          width: 100%;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .profile-form{
    grid-template-columns: 100%;
  }

  .back-link{
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }

  .address-card{
    padding-bottom: 4.8125em;
    position: relative;
    &__nav{
      position: absolute;
      bottom: 1.0625em;
      left: 1.0625em;
    }
    &__street{
      max-width: 100%;
    }
    &__bottom{
      @include grid100gap(1.25em);
    }
  }
}
