.catalog{
  padding: 0 0 6.25em;
  &__grid{
    display: grid;
    grid-template-columns: 19.6875em calc(100% - 19.6875em - 1.25em);
    gap: 1.25em;
  }
}

.catalog-search{
  margin-top: 1em;
}

.filter-btn{
  display: none;
}

.selected-filters{
  display: flex;
  flex-wrap: wrap;
  width: calc(100% + .625em);
  margin-top: .9375em;
  .selected-item{
    margin: .625em .625em 0 0;
  }
}

.selected-item{
  display: flex;
  align-items: center;
  padding: 0 1.81em;
  height: 3.1875em;
  background: #F3F3F8;
  border-radius: 0.25em;
  color: $black2;
  &._all{
    border: 1px solid #F2F6F8;
    background: transparent;
    font-weight: 500;
    &:hover{
      background: $black;
      color: #fff;
      path{
        stroke: #fff;
      }
    }
    span{
      transform: translateY(-15%);
    }
  }
  span{
    font-size: 1em;
    line-height: 110%;
  }
  &__remove{
    margin-left: .625em;
    width: .5625em;
    flex-shrink: 0;
    @include center;
    svg{
      width: 100%;
      height: 100%;
    }
    &:hover{
      path{
        opacity: 1;
        stroke: $primary;
      }
    }
  }
}

.catalog-top{
  margin-top: 1.56em;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.sort{
  display: flex;
  align-items: center;
  .fg{
    margin-left: .875em;
    min-width: 17.625em;
  }
}

.count-pagination{
  display: flex;
  align-items: center;
  &__items{
    display: flex;
    align-items: center;
    margin-left: .625em;
  }
  &__btn{
    margin-left: .625em;
    width: 3.125em;
    height: 2.8125em;
    @include center;
    border: 1px solid #F3F7F9;
    color: $black;
    span{
      font-size: 1em;
    }
    &:hover,
    &._active{
      border-color: $black;
      color: $black;
    }
  }
}

.catalog-items{
  margin-top: 1.56em;
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
  gap: 1.25em;
}

.pagination{
  margin-top: 3.75em;
  &__items{
    display: flex;
    justify-content: center;
  }
  &__item{
    height: 3.625em;
    min-width: 3.625em;
    padding: 0 1.25em;
    border: 1px solid #F2F6F8;
    @include center;
    margin: 0 .3125em;
    font-weight: 500;
    svg{
      width: 1.0625em;
      height: auto;
    }
    &._active{
      pointer-events: none;
    }
    &:hover,
    &._active{
      color: #fff;
      background-color: $black;
      border-color: $black;
      path{
        fill: #fff;
      }
    }
    &._btn{
      padding: 0 1.5em;
    }
  }
}

.filter{
  &__nav,
  &__bg,
  &__close{
    display: none;
  }
}

.filter-group{
  border: 1px solid #F2F6F8;
  padding: 1.875em;
  &:not(:last-child){
    border-bottom: none;
  }
  &._open{
    .filter-group__top svg{
      transform: rotate(-180deg);
    }
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: all .3s;
    &:hover{
      cursor: pointer;
      color: $primary;
    }
    svg{
      width: .5em;
      height: auto;
      margin-left: .5em;
    }
  }
  &__title{
    font-size: 1.125em;
    line-height: 128%;
    text-transform: uppercase;
    font-weight: 700;
    font-family: $font1;
  }
  &__bottom{
    margin-top: 1.5em;
    display: none;
  }
  &__checks{
    max-height: 18.5em;
    @include grid100gap(.56em);
    overflow-y: auto;
    width: calc(100% + 1.1875em);
    padding-right: 1.1875em;
    &::-webkit-scrollbar {
      width: 0.1875em;
      height: 0.1875em;
    }
    &::-webkit-scrollbar-track {
      background: #E8E8E8;
      border-radius: 0;
    }
    &::-webkit-scrollbar-thumb {
      background: #676767;
    }
  }
}

.filter-slider{
  &__item{
    min-height: .9375em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
  }
  .range-slider{
    width: 100%;
    background-color: #E7EAEA;
    border: none;
    box-shadow: none;
    height: .125em;
    border-radius: 0;
    .noUi-connect{
      background-color: $primary;
    }
    .noUi-handle{
      box-shadow: none;
      border: none;
      background-color: $primary;
      width: .22em;
      height: .9375em;
      top: -.34375em !important;
      right: -.11em !important;
      border-radius: 0;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        background: transparent;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.5em;
  }
  &__output{
    font-size: .875em;
    color: #3F4244;
    display: flex;
    flex-wrap: wrap;
    margin-top: -.25em;
    b{
      font-weight: 500;
      white-space: nowrap;
    }
    &-item{
      margin-top: .25em;
      padding-right: .0625em;
    }
  }
  &__btn{
    border-radius: 0.25em;
    background: #F3F3F8;
    padding: 0 .875em;
    height: 3.0625em;
    font-family: $font3;
    font-weight: 700;
    text-transform: uppercase;
    span{
      font-size: .8125em;
    }
    &:hover{
      background: $primary;
      color: #fff;
    }
  }
}

.filter-check{
  input:checked{
    &~label{
      .filter-check__num{
        color: #585858;
      }
    }
  }
  label{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:before{
      top: 50%;
      transform: translateY(-50%);
    }
    .filter-check__title{
      font-size: .9375em;
      line-height: 193%;
      color: $black2;
    }
    .filter-check__num{
      color: #9F9F9F;
      font-size: .8125em;
    }
  }
}

.info{
  margin-top: 5em;
  .text{
    margin-top: 1.875em;
    column-count: 2;
    column-gap: 3em;
    font-weight: 400;
    a{
      color: $primary;
      font-weight: 700;
    }
  }
}

.empty-catalog{
  margin-top: 1.56em;
  padding: .75em .875em;
  justify-content: center;
  border-radius: 0.1875em;
  border: 1px solid #D2E5EC;
  background: #F2F8FA;
  color: #455358;
}


@media screen and (max-width: 992px){
  .catalog{
    padding-bottom: 5em;
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: 14.4375em calc(100% - 14.4375em - 1.25em);
    }
    .catalog-search{
      margin-top: 0;
      input{
        height: 3.75em;
      }
    }
  }

  .selected-filters{
    margin-top: .8em;
    width: calc(100% + .44em);
    .selected-item{
      margin: .44em .44em 0 0;
    }
  }

  .selected-item{
    height: 2.4375em;
    padding: 0 .875em;
  }

  .catalog-top{
    margin-top: 1.25em;
  }

  .count-pagination{
    display: none;
  }

  .sort{
    width: 100%;
    &__title{
      display: none;
    }
    .fg{
      width: 100%;
      margin: 0;
    }
  }

  .catalog-items{
    margin-top: 1.875em;
    grid-template-columns: repeat(2,calc(50% - .9375em / 2));
    gap: .9375em;
  }

  .pagination{
    margin-top: 3.44em;
    ._hide-tablet{
      display: none;
    }
    ._btn{
      width: 3.625em;
      padding: 0;
    }
  }

  .filter-group{
    padding: 1.25em .9375em;
    &__checks{
      width: calc(100% + .375em);
      padding-right: .75em;
    }
  }

  .filter-slider{
    &__bottom{
      display: block;
    }
    &__btn{
      width: 100%;
      margin: .44em 0 0;
    }
  }

  .info{
    margin-top: 2.5em;
    .text{
      margin-top: 1.56em;
      column-count: 1;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
    }
  }

  .filter-btn{
    margin-bottom: 1.25em;
    display: flex;
    align-items: center;
    padding: 0 1.56em;
    border: 1px solid #F2F6F8;
    height: 3.75em;
    width: 100%;
    font-family: $font1;
    &__icon{
      width: 1.125em;
      flex-shrink: 0;
      margin-right: .5em;
    }
    &__title{
      font-size: 1.125em;
      line-height: 111%;
      text-transform: uppercase;
      font-weight: 700;
    }
  }

  .filter{
    @include menu;
    &__nav{
      padding: .94em 1.25em;
      @include grid100gap(.625em);
    }
    &__btn{
      height: 3.3125em;
      width: 100%;
      font-weight: 500;
      text-transform: uppercase;
      span{
        font-size: 1.0625em;
      }
    }
  }

  ._open-filter{
    .filter{
      transform: translateX(0);
      &__bg{
        display: block;
      }
      &__close{
        display: flex;
      }
    }
  }

  .filter-group{
    padding: 1.875em 1.5625em;
    &__checks{
      width: calc(100% + .9375em);
      padding-right: .9375em;
    }
  }

  .info{
    .line-title{
      width: calc(100% + .5em);
    }
  }
}
