.about-page{
  padding-bottom: 3.125em;
  &__inner{
    position: relative;
  }
  &__content{
    max-width: 40.0625em;
    min-height: 27.3125em;
  }
  .text{
    margin-top: 1.875em;
    img{
      position: absolute;
      right: 0;
      top: 0;
      width: calc(100% - 40.0625em - 3.25em);
    }
    p{
      font-size: 1.0625em;
    }
  }
}

.about2{
  padding: 3.125em 0;
  &__text2{
    margin-top: 1em;
    column-count: 2;
    column-gap: 3.125em;
    a{
      font-weight: 700;
      color: $primary;
    }
  }
}

.invite{
  margin-top: 3.125em;
  border: .6875em solid $lightGray;
  padding: 1.5em;
  @include grid100gap(.9375em);
  .text41{
    text-align: center;
  }
  .text{
    font-weight: 400;
    a{
      color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .about-page{
    padding-bottom: 2.5em;
    &__content{
      max-width: 100%;
    }
    .text{
      margin-top: .9375em;
      img{
        position: static;
        width: 14.5625em;
        float: right;
        margin-left: 1.81em;
      }
    }
  }

  .about2{
    padding: 2.5em 0;
    &__text2{
      margin-top: .75em;
      column-count: 1;
    }
  }

  .invite{
    margin-top: 2.5em;
    border-width: .4375em;
    padding: .8125em;
  }
}


@media screen and (max-width: 576px){
  .about-page{
    padding-bottom: 3.7em;
    &__inner{
      padding-bottom: 14.5em;
    }
    .text{
      img{
        position: absolute;
        width: 100%;
        bottom: 0;
        top: unset;
        margin: 0;
      }
    }
  }
}
