@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.swiper-button{
  border-radius: 0.25em;
  border: 1px solid #F8F9F9;
  width: 3.25em;
  height: 3.25em;
  @include center;
  opacity: 1 !important;
  pointer-events: unset !important;
  &:hover{
    background: #252627;
    border-color: #252627;
    path{
      fill: #fff;
    }
  }
  svg{
    width: .5em;
    height: auto;
  }
}

.swiper-nav{
  display: flex;
  align-items: center;
  .swiper-button{
    position: static;
    margin: 0 0 0 .9375em;
  }
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}


@media screen and (max-width: 992px){
  .swiper-button{
    width: 2.75em;
    height: 2.75em;
  }

  .swiper-nav{
    .swiper-button{
      margin: 0 0 0 .625em;
    }
  }
}
