.cart{
  padding-bottom: 2.25em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 19.6875em - 1.25em) 19.6875em;
    gap: 1.875em 1.25em;
    margin-top: 2.5em;
  }
}

.cart-sum{
  position: sticky;
  top: 1em;
  border: .5em solid $lightGray;
  padding: 1.0625em;
  @include grid100gap(.9375em);
  &__props{
    @include grid100gap(.9375em);
  }
  &__btn{
    padding: 0 1em !important;
    img{
      margin-left: .9375em !important;
    }
  }
}

.prop-item{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &:after{
    content: '';
    flex-grow: 1;
    height: 1px;
    min-width: .375em;
    border-top: 1px dashed #D1D1D1;
    margin: 0 .94em;
  }
  &__title{
    color: $black3;
    &._big{
      font-weight: 500;
      font-size: 1.125em;
      line-height: 132%;
    }
  }
  &__desc{
    order: 2;
    &._big{
      font-size: 1.5em;
      font-weight: 500;
    }
  }
  &__title,
  &__desc{
    font-size: 1.0625em;
    line-height: 147%;
  }
}

.cart-row{
  padding: 1.56em .9375em;
  display: grid;
  grid-template-columns: calc(100% - 30.5em - 2.8125em) 8em 10.5em 8em 2.8125em;
  gap: 1.25em 1em;
  align-items: center;
  &:nth-child(even){
    background: #FAFAFB;
    .cartcalc{
      button{
        &:not(:hover){
          background-color: #fff;
        }
      }
    }
  }
  &__info{
    display: grid;
    grid-template-columns: 6.375em calc(100% - 6.375em - .9375em);
    gap: .9375em;
    align-items: center;
  }
  &__img{
    @include imgCover(75.4%);
    border-radius: 0.1875em;
    background-color: #fff;
    border: $border;
  }
  &__content{
    max-width: 17.18em;
    @include grid100gap(.375em);
  }
  &__title{
    font-size: 1.1875em;
    line-height: 121%;
    font-weight: 500;
  }
  &__articul{
    font-size: .875em;
    line-height: 200%;
    color: #5C6165;
  }
  &__price{
    @include grid100gap(.13em);
    &-title{
      font-size: .9375em;
      line-height: 133%;
      color: $black3;
    }
  }
  &__remove{
    width: 2.8125em;
    height: 2.8125em;
    border-radius: 50%;
    @include center;
    &:hover{
      background-color: $lightGray;
    }
    svg{
      width: 100%;
      height: auto;
    }
  }
}

.cart-empty{
  padding-bottom: 6.25em;
  &__grid{
    margin-top: 4.37em;
    display: flex;
    align-items: center;
  }
  &__icon{
    flex-shrink: 0;
    width: 10.625em;
    height: 10.625em;
    border-radius: 50%;
    margin-right: 1.875em;
    background-color: $lightGray;
    position: relative;
    @include center;
    img{
      width: 4.875em;
    }
    ._pin{
      position: absolute;
      top: .5em;
      right: .75em;
      min-width: 2.125em;
      height: 2.125em;
      span{
        font-size: .9375em;
      }
    }
  }
  &__info{
    @include grid100gap(.75em);
    margin-right: 1.25em;
  }
  &__text{
    font-size: 1.0625em;
    line-height: 147%;
    a{
      font-weight: 700;
    }
  }
  &__btn{
    margin-left: auto;
    width: 21.0625em;
    flex-shrink: 0;
  }
}

.favourite{
  padding-bottom: 6.25em;
  @media screen and (min-width: 993px){
    &:not(._col3) .catalog-items{
      grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    }
  }
  .catalog-items{
    margin-top: 2.5em;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: 19.6875em calc(100% - 19.6875em - 1.25em);
    gap: 1.875em 1.25em;
    .catalog-items{
      margin-top: 0;
    }
  }
}

.favourite-empty{
  .cart-empty__icon{
    img{
      width: 3.0625em;
    }
  }
}

.favourite-menu{
  position: sticky;
  top: 1em;
  @include grid100gap(1.56em);
  &__btn{
    padding: 0;
    width: 100%;
    height: 4.0625em;
    font-weight: 500;
    text-transform: uppercase;
    span{
      font-size: 1.0625em;
    }
  }
}

.favourite-nav{
  border-radius: 0.1875em;
  border: .5em solid $lightGray;
  padding: 1.6875em;
  &__hidden{
    margin-top: .9375em;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}

.favourite-link{
  display: flex;
  align-items: center;
  color: $black2;
  @include lastM(0 0 .625em);
  &._active{
    .favourite-link__title{
      font-weight: 500;
    }
  }
  &__title{
    font-size: 1.125em;
    line-height: 156%;
  }
  &__num{
    margin-left: .5em;
    font-size: .9375em;
    line-height: 187%;
    color: rgba($black3,.3);
  }
}

.favourite-remove{
  @include center;
  font-weight: 500;
  transition: all .3s;
  &:hover{
    color: $red;
    .favourite-remove__icon{
      background-color: $red;
      path{
        stroke: #fff;
      }
    }
  }
  &__title{
    font-size: 1.0625em;
    line-height: 165%;
  }
  &__icon{
    transition: inherit;
    width: 2.8125em;
    height: 2.8125em;
    border-radius: 50%;
    @include center;
    background-color: $lightGray;
    margin-right: .625em;
    flex-shrink: 0;
    svg{
      width: 100%;
      height: auto;
    }
  }
}


@media screen and (max-width: 992px){
  .cart{
    padding-bottom: 1.562em;
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1.875em;
    }
  }

  .cart-items{
    width: calc(100% + 3.125em);
    margin-left: -1.5625em;
  }

  .cart-row{
    padding: 1.56em;
    grid-template-columns: calc(100% - 19.5em - 2.8125em) 8.5em 8em 2.8125em;
    ._hide-tablet{
      display: none;
    }
  }

  .cart-empty{
    padding-bottom: 5em;
  }

  .cart-empty{
    padding-bottom: 5em;
    &__grid{
      margin-top: 1.875em;
    }
    &__icon{
      width: 6.5em;
      height: 6.5em;
      img{
        width: 3em;
      }
      ._pin{
        top: .31em;
        right: .46em;
        min-width: 1.3em;
        height: 1.3em;
        span{
          font-size: .875em;
        }
      }
    }
    &__info{
      gap: .625em;
    }
    &__btn{
      width: 14.0625em;
      padding: 0 !important;
    }
  }

  .favourite{
    padding-bottom: 5em;
    .catalog-items{
      margin-top: 1.875em;
    }
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1.875em;
      .catalog-items{
        margin-top: 0;
      }
    }
    &__sidebar{
      position: relative;
      z-index: 3;
    }
    @media screen and (min-width: 577px){
      .catalog-items{
        grid-template-columns: repeat(3,calc(100% / 3 - .625em));
        gap: .9375em;
      }
    }
  }

  .favourite-empty{
    .cart-empty__icon{
      img{
        width: 1.8125em;
      }
    }
  }

  .favourite-menu{
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
    &__btn{
      span{
        font-size: 1em;
      }
    }
  }

  .favourite-nav{
    position: relative;
    padding: 0;
    border: none;
    z-index: 3;
    &._open{
      .favourite-nav__hidden{
        opacity: 1;
        pointer-events: unset;
      }
      .favourite-nav__show:after{
        transform: rotate(-180deg);
      }
    }
    &__show{
      border-radius: 0.1875em;
      border: .4375em solid $lightGray;
      height: 4.0625em;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 .8125em;
      &:after{
        content: '';
        flex-shrink: 0;
        width: .5em;
        height: .75em;
        transition: all .2s;
        background-image: url(../img/select-arrow.svg);
        @include bgImgSize(100%);
      }
    }
    &__hidden{
      position: absolute;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      top: 100%;
      left: 0;
      background-color: #fff;
      border: .4375em solid $lightGray;
      padding: .8125em;
      width: 100%;
      z-index: 3;
      margin-top: -.4375em;
    }
  }

  .favourite-remove{
    justify-content: unset;
  }
}


@media screen and (max-width: 576px){
  .cart-items{
    width: calc(100% + 1.875em);
    margin-left: -.9375em;
  }

  .cart-row{
    padding: 1.56em .9375em;
    grid-template-columns: calc(100% - 10.875em - 2.8125em) 8.875em 2.8125em;
    &__info{
      grid-column: 1/-1;
    }
    &__action,
    &__count{
      order: 2;
    }
  }

  .cart-empty{
    &__grid{
      flex-direction: column;
      text-align: center;
    }
    &__icon{
      margin: 0;
    }
    &__info{
      margin: 1.875em 0 0;
    }
    &__btn{
      width: 100%;
      margin: 1.25em 0 0;
    }
  }

  .favourite{
    &__top{
      flex-direction: column;
      align-items: flex-start;
      .back-link{
        order: -1;
        margin-bottom: .625em;
      }
    }
  }

  .favourite-menu{
    grid-template-columns: 100%;
  }

  .favourite-nav{
    &__show{
      height: 3.8125em;
    }
  }

  .favourite-remove{
    justify-content: center;
  }
}
