.order{
  padding-bottom: 6.25em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 26.6875em - 1.25em) 26.6875em;
    gap: 1.875em 1.25em;
    margin-top: 2.5em;
  }
  &__content{
    align-self: flex-start;
    @include grid100gap(.9375em);
  }
  .empty-catalog{
    margin: 0;
    color: #455358;
  }
  &._disabled{
    .m-check{
      opacity: 0.5;
      pointer-events: none;
    }
    .fg{
      textarea,
      input{
        background: #F6F6F6;
        pointer-events: none;
      }
    }
  }
}

.order-form{
  padding: 1.56em;
  border-radius: 0.1875em;
  border: $border;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .78em));
  gap: 1.56em;
  &__checks{
    @include grid100gap(.625em);
  }
}

.order-sum{
  position: sticky;
  top: 1em;
  padding: 1.6875em;
  border: .5em solid $lightGray;
  @include grid100gap(.9375em);
  &__items{
    @include grid100gap(.9375em);
  }
  &__payment{
    @include grid100gap(.625em);
    position: relative;
    z-index: 3;
  }
}

.order-row{
  display: grid;
  grid-template-columns: 3.875em calc(100% - .9375em - 3.875em);
  gap: .9375em;
  align-items: flex-start;
  &__img{
    @include imgCover(75.4%);
    border-radius: 0.1875em;
    background-color: #fff;
    border: $border;
  }
  &__title{
    font-size: 1em;
    line-height: 112%;
    font-weight: 500;
  }
  .cartcalc{
    width: 5.625em;
    grid-template-columns: 1.625em calc(100% - 3.25em) 1.625em;
    height: 2.6875em;
  }
  &__nums{
    margin-top: .56em;
    display: flex;
    align-items: center;
  }
  &__count{
    margin-right: .9375em;
  }
}

.is-reg{
  display: flex;
  align-items: center;
  padding: .625em 1.56em;
  border-radius: 0.1875em;
  border: $border;
  &__img{
    width: 1.6875em;
    flex-shrink: 0;
    margin-right: .625em;
  }
  .text17{
    margin-right: .81em;
  }
  &__btn{
    padding: 0 1.125em;
    font-weight: 700;
    span{
      font-size: .9375em;
    }
  }
}

._open-auth{
  .auth-modal{
    transform: translateX(0);
    &__wrap{
      opacity: 1;
      pointer-events: unset;
    }
    &__bg{
      display: block;
    }
    &__close{
      display: flex;
    }
  }
}

.auth-modal{
  @include menu;
  padding: 3.125em 1.56em;
  @media screen and (min-width: 993px){
    transform: translateX(120%);
    position: absolute;
    left: unset;
    right: 0;
    width: 24.5em;
    &__wrap{
      opacity: 0;
      pointer-events: none;
      position: fixed;
      top: 0;
      right: 0;
      width: 100%;
      height: $vh;
      z-index: 100;
      overflow: hidden;
    }
    &__close{
      right: 25.5em;
    }
  }
  &__close,
  &__bg{
    display: none;
  }
  &__close{
    opacity: 0.4;
    &:hover{
      opacity: 1;
    }
  }
  &__title{
    text-align: center;
    font-size: 1.875em;
    text-transform: uppercase;
    font-weight: 700;
  }
}

.auth-form{
  margin-top: 1.81em;
  @include grid100gap(1.125em);
  &__btn{
    margin-top: .42em;
    padding: 0 !important;
  }
  &__check{
    margin-top: .12em;
    display: flex;
    justify-content: center;
  }
}

.order-success{
  padding-bottom: 6.25em;
  &__inner{
    margin: 2.5em auto 0;
    width: 60.375em;
    max-width: 100%;
    @include grid100gap(2.31em);
  }
  &__badge{
    @include center;
    padding: 1.06em;
    border-radius: 0.1875em;
    background-color: $lightGray;
    @include center;
    img{
      width: 1.5625em;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
}

.cart-info{
  padding: 2.5em;
  border-radius: 0.25em;
  border: 1px solid #F6F6F6;
  display: flex;
  justify-content: space-between;
  &__item{
    display: flex;
    align-items: center;
    &:not(:last-child){
      @media screen and (min-width: 993px){
        flex-grow: 1;
        &:after{
          content: '';
          height: 4.625em;
          width: 1px;
          border-right: 1px dashed #D7D7D7;
          min-height: 100%;
          flex-shrink: 0;
          margin: 0 auto;
        }
      }
    }
    &-inner{
      @include grid100gap(.44em);
    }
  }
  &__title{
    font-size: .875em;
    line-height: 129%;
    color: #4C4C4C;
  }
  &__desc{
    font-size: 1.25em;
    line-height: 120%;
    font-weight: 500;
  }
  &__badge{
    display: flex;
    align-items: center;
    color: #11A342;
    font-weight: 500;
    &:before{
      content: '';
      width: 0.3125em;
      height: 0.3125em;
      flex-shrink: 0;
      margin-right: .56em;
      border-radius: 50%;
      background-color: #11A342;
    }
    .text20{
      line-height: 120%;
    }
  }
}

.success-table{
  &__head{
    padding: 0 .625em 1.25em;
    display: flex;
    justify-content: space-between;
  }
  &__rows{
    padding: 0 1.56em;
    border: $border;
  }
}

.order-row2{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.44em 0;
  &._sum{
    align-items: flex-start;
    padding: 1.25em 0;
  }
  &:not(:last-child){
    border-bottom: $border;
  }
  &__info{
    display: flex;
    align-items: center;
  }
  &__title{
    font-size: 1em;
    line-height: 112%;
  }
  &__title2{
    font-size: 1em;
    line-height: 125%;
    font-weight: 500;
  }
  &__num{
    margin-left: 1em;
    font-size: .875em;
    line-height: 128%;
    color: #7A7A7E;
    font-weight: 300;
  }
  &__price{
    font-size: 1.25em;
    line-height: 105%;
    font-weight: 500;
    white-space: nowrap;
    margin-left: 1em;
  }
  &__desc{
    font-size: 1.0625em;
    line-height: 141%;
    color: $black3;
  }
  &__title3{
    font-size: 1.5em;
    font-weight: 500;
  }
  &__price2{
    font-size: 1.8125em;
    font-weight: 500;
    line-height: 141%;
    color: $primary;
    white-space: nowrap;
  }
}


@media screen and (max-width: 992px){
  .order{
    padding-bottom: 5em;
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1.875em;
    }
  }

  .order-form{
    padding: 1.56em .9375em;
  }

  .order-sum{
    border-width: .4375em;
    padding: .8125em;
  }

  .is-reg{
    padding: .625em .875em;
    &__btn{
      margin-left: auto;
    }
    &__text{
      font-size: 1em;
    }
  }

  .order-success{
    padding-bottom: 5em;
    &__badge{
      padding: .94em;
    }
  }

  .cart-info{
    padding: 1.25em .9375em;
    &__desc{
      font-size: 1.125em;
    }
  }

  .success-table{
    &__rows{
      padding: 0 .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .order-form{
    grid-template-columns: 100%;
  }

  .is-reg{
    align-items: flex-start;
  }

  .cart-info{
    @include grid100gap(.31em);
  }

  .order-success{
    &__badge{
      text-align: center;
      flex-direction: column;
      img{
        margin: 0 0 .625em;
      }
    }
  }

  .order-row2{
    align-items: unset;
    &._sum{
      align-items: center;
    }
    &__info{
      align-items: flex-start;
    }
  }
}
