.news-page{
  padding-bottom: 6.25em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 3.75em 1.25em;
    margin-top: 2.5em;
  }
}

.links{
  margin-top: 2.5em;
  display: flex;
}

.m-link{
  display: flex;
  align-items: center;
  padding: 0 1.81em;
  height: 3.0625em;
  border-radius: 0.25em;
  background: #F3F3F8;
  text-transform: uppercase;
  color: $black2;
  font-weight: 500;
  flex-shrink: 0;
  @include lastM(0 .625em 0 0);
  span{
    font-size: .8125em;
    line-height: 130%;
  }
  &:hover,
  &._active{
    background: $black;
    color: #fff;
  }
}

.news-item{
  padding-bottom: 3.75em;
  &__top{
    display: flex;
    align-items: center;
    margin-top: .5em;
    .m-badge{
      margin-left: .625em;
    }
  }
  &__date{
    font-size: .875em;
    line-height: 164%;
    color: #343738;
  }
  .text{
    font-weight: 400;
    margin-top: 1.875em;
    color: $black3;
    a{
      color: $primary;
    }
  }
  .doc{
    @include lastM(0 0 1em);
  }
}

.video-block{
  display: grid;
  grid-template-columns: calc(100% - 35.75em) 35.125em;;
  gap: .625em;
  @include lastM(0 0 1em);
  &__youtube{
    top: 1em;
    position: sticky;
    padding-bottom: 56.2%;
    iframe{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 0.25em;
    }
  }
}

.m-table{
  @include lastM(0 0 1em);
  table{
    width: 100%;
    border-collapse: collapse;
  }
  th,td{
    padding: 0 1.625em;
    border: 1px solid #EFF1F1;
  }
  th{
    background: #F5F7F8;
    height: 3.375em;
    text-align: left;
    font-weight: 500;
    span{
      font-size: 1.4375em;
      line-height: 122%;
    }
  }
  td{
    padding-top: .81em;
    padding-bottom: .81em;
    span{
      font-size: 1.0625em;
      line-height: 147%;
    }
  }
}

.img-block{
  display: grid;
  grid-template-columns: 26.0625em calc(100% - 26.0625em - 1.875em);
  gap: 1.875em;
  @include lastM(0 0 1em);
  &__media{
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    text-align: center;
    font-size: .9375em;
    line-height: 167%;
    color: #868C8F;
    margin-top: .875em;
  }
}

.doc{
  display: flex;
  align-items: center;
  img{
    width: 2.5em;
    margin: 0 .69em 0 0 !important;
  }
  &__content{
    @include grid100gap(.125em);
  }
  &__title{
    font-size: 1.0625em;
    font-weight: 500;
    line-height: 123%;
  }
  &__info{
    font-size: .9375em;
    color: $black3;
    line-height: 127%;
  }
}


@media screen and (max-width: 992px){
  .news-page{
    padding-bottom: 5em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.56em 1.25em;
      margin-top: 1.56em;
    }
  }

  .links{
    margin-top: 1.56em;
    padding-bottom: 1em;
    margin-bottom: -1em;
    @include fullScreenTablet;
    overflow-x: auto;
  }

  .m-link{
    padding: 0 .625em;
    height: 2.25em;
    span{
      font-size: .6875em;
    }
  }

  .news-item{
    padding-bottom: 2em;
    .text{
      margin-top: 1.25em;
    }
  }

  .video-block{
    grid-template-columns: calc(100% - 20.625em - 1.25em) 20.625em;
    gap: .625em 1.25em;
  }

  .m-table{
    th{
      span{
        font-size: 1.125em;
      }
    }
    td{
      span{
        font-size: .9375em;
      }
    }
  }

  .img-block{
    grid-template-columns: 20.625em calc(100% - 20.625em - 1.25em);
    gap: 1.875em 1.25em;
    &__img{
      position: sticky;
      top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .news-page{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .links{
    @include fullScreenMob;
  }

  .video-block,
  .img-block{
    grid-template-columns: 100%;
  }
}
