.profile{
  padding: 0 0 6.25em;
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: 19.6875em calc(100% - 19.6875em - 1.25em);
    gap: 1.875em 1.25em;
  }
}

.lk-menu{
  position: sticky;
  top: 1em;
  &__bg,
  &__close{
    display: none;
  }
}

.lk-nav{
  background: $black;
  color: #fff;
}

.lk-link{
  min-height: 4.625em;
  display: flex;
  align-items: center;
  padding: 0 1.56em;
  color: #fff;
  border-bottom: 1px solid rgba(255, 255, 255, 0.04);
  &:last-child{
    border: none;
  }
  &._active,
  &:hover{
    border-color: $primary;
    background-color: $primary;
    color: #fff;
    path{
      opacity: 1;
    }
    .lk-link__count{
      background-color: $black;
    }
  }
  &__icon{
    width: 1.625em;
    flex-shrink: 0;
    margin-right: .5em;
    @include center;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__title{
    font-size: 1.0625em;
    line-height: 117%;
    font-weight: 500;
    text-transform: uppercase;
    font-family: $font1;
    ._view2{
      display: none;
    }
  }
  &__count{
    position: static;
    margin-left: auto;
    transform: unset;
    width: 1.5em;
    height: 1.5em;
    border: none;
    span{
      font-size: .8125em;
    }
  }
}

.menu-btn{
  display: none;
}

.manager{
  @include grid100gap(.625em);
  padding: 1.5em;
  border: .6875em solid $lightGray;
  &__wrap,
  &__info{
    display: flex;
    align-items: center;
  }
  &__wrap{
    justify-content: space-between;
  }
  .avatar{
    margin-right: .9375em;
  }
  &__name{
    line-height: 125%;
  }
  &__prof{
    color: #61676A;
    line-height: 125%;
    margin-top: .125em;
  }
}

.manager-badge{
  padding: .625em 1.125em;
  border-radius: 0.1875em;
  border: 1px solid $primary;
  display: flex;
  align-items: center;
  &._blue{
    border-color: #235AAC;
    .manager-badge__type{
      color: #235AAC;
    }
  }
  &._green{
    border-color: #36AC23;
    .manager-badge__type{
      color: #36AC23;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 175%;
  }
  &__icon{
    width: 1.5em;
    flex-shrink: 0;
    margin: 0 .375em;
  }
  &__type{
    font-size: 1.0625em;
    line-height: 165%;
    font-weight: 500;
    color: $primary;
  }
}

.manager-contacts{
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  &__group{
    @include grid100gap(.3125em);
  }
  &__title{
    color: #87878D;
    font-size: 1.0625em;
    line-height: 118%;
  }
  &__link{
    display: flex;
    align-items: center;
    img{
      width: 1.1875em;
      flex-shrink: 0;
      margin-right: .31em;
    }
    span{
      font-size: 1em;
      line-height: 175%;
    }
  }
  &__phone{
    font-weight: 500;
    color: $black2;
    span{
      font-size: 1.1875em;
      line-height: 147%;
    }
  }
  &__mail{
    color: $primary;
    span{
      text-decoration: underline;
    }
    &:hover{
      span{
        text-decoration-color: transparent;
      }
    }
  }
}

.dashboard-orders{
  margin-top: 3.125em;
  @include grid100gap(1.56em);
  .empty-catalog{
    margin: 0;
    justify-content: unset;
  }
}

.order-table{
  table{
    border-collapse: collapse;
    width: 100%;
  }
  th{
    border-top: $border;
    text-align: left;
    font-weight: 500;
    height: 3.75em;
    font-family: $font1;
  }
  th,td{
    border-bottom: $border;
    padding: .9375em 0 .9375em 1.25em;
    &:first-child{
      border-left: $border;
    }
    &:nth-child(3){
      width: 19.375em;
    }
    &:last-child{
      border-right: $border;
      padding-right: 1.25em;
      width: 5.625em;
    }
  }
  &__title{
    font-size: 1.125em;
    line-height: 111%;
    font-weight: 500;
    text-transform: uppercase;
  }
}

.order-row3{
  td{
    vertical-align: top;
  }
  &__title{
    display: none;
  }
  &__hint{
    max-width: 18.4375em;
    font-size: .9375em;
    line-height: 133%;
    color: #4C5255;
    margin-top: .4em;
  }
  &__price{
    font-size: 1.3125em;
    line-height: 143%;
    font-weight: 500;
    white-space: nowrap;
  }
  &__link{
    width: 3.125em;
    height: 3.125em;
    svg{
      width: .4375em;
      height: auto;
    }
    span{
      display: none;
    }
    &:hover{
      path{
        fill: #fff;
      }
    }
  }
}

.order-badge{
  display: inline-flex;
  align-items: center;
  padding: 0 .75em;
  height: 2.125em;
  border: 1px solid $primary;
  color: $primary;
  font-weight: 500;
  text-transform: uppercase;
  &._green{
    color: #11A342;
    border-color: #11A342;
    &:before{
      background-color: #11A342;
    }
  }
  &._red{
    color: #F81D2A;
    border-color: #F81D2A;
    &:before{
      background-color: #F81D2A;
    }
  }
  &._orange{
    color: #FF7337;
    border-color: #FF7337;
    &:before{
      background-color: #FF7337;
    }
  }
  &:before{
    content: '';
    width: 0.3125em;
    height: 0.3125em;
    flex-shrink: 0;
    margin-right: .56em;
    border-radius: 50%;
    background-color: $primary;
  }
  span{
    font-size: .6875em;
    line-height: 130%;
  }
}


@media screen and (max-width: 992px){
  .profile{
    padding-bottom: 5em;
    &__grid{
      margin-top: 1.875em;
      grid-template-columns: 100%;
    }
  }

  @media screen and (min-width: 577px){
    .lk-nav{
      display: grid;
      grid-template-columns: repeat(7,calc(100% / 7));
    }

    .lk-link{
      position: relative;
      padding: .9375em .44em;
      flex-direction: column;
      text-align: center;
      border-bottom: none;
      border-right: 1px solid rgba(255, 255, 255, 0.04);
      &__icon{
        margin: 0 0 .875em;
      }
      &__count{
        position: absolute;
        top: -.625em;
        right: 0;
      }
      &__title{
        font-size: .8125em;
        line-height: 123%;
        min-height: 2.46em;
        display: flex;
        align-items: center;
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
  }

  .dashboard-orders{
    margin-top: 1.875em;
  }

  .order-table{
    th,td{
      padding-left: .9375em;
      &:nth-child(3){
        width: 15.125em;
      }
      &:last-child{
        padding-right: .9375em;
        width: 5em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .profile{
    &__grid{
      gap: 0;
    }
  }

  .lk-menu{
    @include menu;
    padding: 3.125em 0;
  }

  .lk-nav{
    background-color: transparent;
  }

  .lk-link{
    background-color: transparent;
    color: $black;
    border-bottom: $border;
    &:last-child{
      border-bottom: $border;
    }
    path{
      stroke: $black3;
    }
    &._active,
    &:hover{
      color: #fff;
      path{
        stroke: #fff;
      }
    }
  }

  ._open-lk-menu{
    .lk-menu{
      transform: translateX(0);
      &__bg{
        display: block;
      }
      &__close{
        display: flex;
      }
    }
  }

  .menu-btn{
    display: flex;
    width: 100%;
    height: 3.5625em;
    border-color: #E3E3E3;
    border-width: 1px;
    font-family: $font3;
    margin-bottom: 1.875em;
    svg{
      width: 1.25em;
      height: auto;
      margin-right: .625em;
    }
    &:hover{
      path{
        stroke: #fff;
      }
    }
  }

  .manager{
    &__wrap{
      flex-direction: column;
      align-items: flex-start;
    }
    &__info{
      margin-bottom: .625em;
      align-items: flex-start;
    }
  }

  .manager-contacts{
    flex-direction: column;
    align-items: flex-start;
    &__group{
      @include lastM(0 0 .9375em);
    }
  }

  .order-table{
    table{
      display: block;
    }
    tbody{
      @include grid100gap(1.56em);
    }
    thead{
      display: none;
    }
    tr{
      @include grid100gap(1em);
    }
    td{
      width: 100% !important;
      padding: 0 !important;
      border: none !important;
    }
  }

  .order-row3{
    padding: .9375em;
    border: $border;
    td{
      display: flex;
      align-items: center;
      justify-content: space-between;
      &:nth-child(3){
        flex-direction: column;
        align-items: flex-start;
        .order-row3__desc{
          margin-top: .625em;
        }
      }
    }
    &__title{
      font-size: .9375em;
      line-height: 133%;
      font-weight: 500;
      display: block;
      text-transform: uppercase;
    }
    &__link{
      width: 100%;
      height: 2.375em;
      border-width: 1px;
      font-family: $font3;
      span{
        display: block;
        font-size: .75em;
        letter-spacing: 0.01em;
      }
      svg{
        margin-left: .3125em;
      }
    }
  }
}
