.header{
  position: relative;
  z-index: 3;
  &__logo{
    display: block;
    width: 5.4375em;
    flex-shrink: 0;
    margin-right: 4.125em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__call{
    padding: 0 .81em;
    border-radius: 0.1875em;
    height: 2.125em;
    font-weight: 500;
    span{
      font-size: .875em;
    }
  }
  &__btn{
    flex-shrink: 0;
    padding: 0 1.56em;
    height: 3.4375em;
    border-radius: 0.1875em;
    text-transform: uppercase;
    font-weight: 700;
    span{
      font-size: .9375em;
      line-height: 120%;
    }
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .625em;
    }
    &--mob{
      display: none;
    }
  }
}

.header-top{
  padding: .375em 0;
  background: #F3F3F8;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__right{
    display: flex;
    align-items: center;
  }
}

.lang{
  position: relative;
  &._open{
    .lang__arrow{
      transform: rotate(-180deg);
    }
    .lang__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    color: #4C4C51;
    transition: all .3s;
    &:hover{
      cursor: pointer;
      color: $primary;
    }
  }
  &__icon{
    width: 1.1875em;
    flex-shrink: 0;
    margin-right: .375em;
  }
  &__arrow{
    margin-left: .375em;
    width: .5em;
    transition: all .3s;
  }
  &__hidden{
    position: absolute;
    top: 100%;
    margin-top: .5em;
    min-width: 100%;
    border: 1px solid #F2F6F8;
    background-color: #fff;
    z-index: 3;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__option{
    padding: .25em .5em;
    display: flex;
    &:not(:last-child){
      border-bottom: 1px solid #F2F6F8;
    }
    span{
      font-size: .875em;
      line-height: 143%;
    }
    &._active{
      background-color: $primary;
      color: #fff;
      border-color: $primary !important;
    }
  }
}

.close-btn{
  width: 1.125em;
  @include center;
  img,
  svg{
    width: 100%;
    height: auto;
  }
}

.menu{
  @include menuDesktop;
  &__top,
  &__call{
    display: none;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-weight: 500;
    text-transform: uppercase;
    color: #505058;
    @include lastM(0 2.5em 0 0);
    span{
      font-size: .875em;
      line-height: 121%;
    }
    &--catalog{
      display: none;
    }
  }
}

.header-contacts{
  font-family: $font2;
  margin-right: .75em;
  position: relative;
  &__show{
    display: flex;
    align-items: center;
    transition: all .3s;
    &:hover{
      color: $primary;
      cursor: pointer;
    }
  }
  &__icon{
    width: 1.1875em;
    flex-shrink: 0;
    margin-right: .625em;
  }
  &__arrow{
    width: .5em;
    flex-shrink: 0;
    margin-left: .375em;
    transition: all .3s;
  }
  &__hidden{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    min-width: 16.6875em;
    position: absolute;
    top: 100%;
    margin-top: .78em;
    padding: 1.562em;
    background-color: #fff;
    z-index: 3;
    box-shadow: 0 .25em 1.8125em 0 rgba(0, 0, 0, 0.25);
    @include grid100gap(.94em);
  }
  &__email,
  &__address{
    display: inline-flex;
    align-items: center;
    margin-right: auto;
  }
  &__email{
    color: $primary;
    &:hover{
      color: $black;
    }
    span{
      text-decoration: underline;
      transition: inherit;
    }
  }
  &__close{
    display: none;
  }
  .header__call{
    display: none;
  }
  &._open{
    .header-contacts__arrow{
      transform: rotate(-180deg);
    }
    .header-contacts__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.header-phone{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__title{
    line-height: 120%;
    color: #898C8D;
  }
  &__icon{
    width: 1.1875em;
    flex-shrink: 0;
    margin-right: .375em;
  }
  &__link{
    display: flex;
    align-items: center;
    margin-top: .44em;
    font-weight: 500;
    .text17{
      line-height: 123%;
    }
  }
}

.header-bottom{
  padding: .81em 0;
  &__inner{
    display: flex;
    align-items: center;
  }
}

.header-action{
  display: flex;
  align-items: center;
  margin-left: auto;
  &__btn{
    position: relative;
    @include lastM(0 1.56em 0 0);
    display: flex;
    align-items: center;
    color: #49494F;
    &:hover{
      color: $primary;
    }
    img{
      width: 1.3125em;
      flex-shrink: 0;
      margin-right: .625em;
    }
    ._pin{
      left: .69em;
      top: -.5em;
      position: absolute;
    }
    &--favourite{
      img{
        width: .875em;
      }
      ._pin{
        left: .33em;
      }
    }
  }
}

._pin{
  min-width: 0.9375em;
  height: 0.9375em;
  padding: 0 .125em;
  background-color: $primary;
  color: #fff;
  @include center;
  border-radius: 50%;
  span{
    font-size: 0.6875em;
    line-height: 120%;
  }
}

.header-search{
  flex-grow: 1;
  margin: 0 2.19em;
}

.m-search{
  position: relative;
  &__result{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    background-color: #fff;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    box-shadow: 0 .4375em 1.625em 0 rgba(0, 0, 0, 0.14);
  }
  &__items{
    @include grid100gap(.625em);
    @include scroll;
    overflow-y: auto;
    max-height: 25em;
  }
  &__nav{
    padding: .625em .9375em;
    border-top: 1px solid $lightGray;
    border-bottom: 1px solid $lightGray;
  }
  &._show{
    z-index: 3;
    .m-search__result{
      opacity: 1;
      pointer-events: unset;
    }
    .search-field{
      input{
        border-color: $black;
      }
    }
  }
}

.search-field{
  position: relative;
  &__btn{
    position: absolute;
    right: .9375em;
    top: 50%;
    transform: translateY(-50%);
    @include center;
    width: 1.1875em;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  input{
    border: 1px solid #D2D2DA;
    width: 100%;
    padding: 0 3.5em 0 .94em;
    height: 3.4375em;
    font-size: 1em;
    line-height: 120%;
    font-family: $font1;
    &::placeholder{
      color: #C5C6C6;
    }
    &:focus{
      border-color: $primary;
    }
  }
}

.search-item{
  display: grid;
  grid-template-columns: 5.5em calc(100% - 13.35em) 6em;
  gap: .9375em;
  align-items: center;
  padding: .625em .9375em;
  &:hover{
    color: $black;
    background: #F9F9F9;
  }
  &:not(:last-child){
    border-bottom: $border;
  }
  &__img{
    border-radius: 0.1875em;
    border: $border;
    @include imgCover(75%);
  }
  &__content{
    @include grid100gap(.125em);
  }
  &__code{
    color: #8C8C97;
  }
}

.empty-search{
  display: flex;
  align-items: flex-start;
  padding: .94em 1.81em;
  color: #455358;
  &__text{
    line-height: 147%;
    b{
      font-weight: 700;
    }
    a{
      text-decoration: underline;
    }
  }
  img{
    width: 1.5em;
    flex-shrink: 0;
    margin-right: .56em;
  }
}


@media screen and (max-width: 992px){
  .header{
    &__logo{
      width: 3.8125em;
      margin-right: 1.31em;
    }
    &__btn{
      height: 2.8125em;
      padding: 0 .81em;
      font-weight: 500;
      span{
        font-size: .875em;
      }
      img{
        width: 1.1875em;
        margin-right: .44em;
      }
      &--mob{
        display: flex;
      }
      &--desktop{
        display: none;
      }
    }
    &._open-menu{
      .menu{
        transform: translateX(0);
        &__bg{
          display: block;
        }
        &__close{
          display: flex;
        }
      }
    }
  }

  .header-top{
    padding: .25em 0;
  }

  .menu{
    @include menu;
    &__call{
      padding: 1.81em .9375em;
      display: flex;
      .header__call{
        font-weight: 700;
        width: 100%;
        border-radius: 0.125em;
        span{
          font-size: .9375em;
        }
      }
    }
    &__top{
      display: block;
      padding: 1.875em .9375em;
    }
  }

  .header-contacts{
    &__hidden{
      left: unset;
      right: 0;
      min-width: 20.0625em;
      margin-top: .5em;
    }
    &__close{
      display: flex;
      position: absolute;
      right: .94em;
      top: .94em;
    }
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    &__link{
      width: 100%;
      height: 3.5625em;
      display: flex;
      align-items: center;
      padding: .94em;
      @include lastM(0);
      border-bottom: 1px solid $lightGray;
      &--catalog{
        display: flex;
        background-color: $lightGray;
        background-image: url(../img/arrow-right.svg);
        background-repeat: no-repeat;
        background-size: .3125em auto;
        background-position: right .94em center;
      }
    }
  }

  .header-bottom{
    padding: .94em 0;
    .header-action{
      &__btn{
        @include lastM(0 1.875em 0 0);
        img{
          margin: 0;
        }
        .text14{
          display: none;
        }
      }
    }
  }

  .header-search{
    margin: 0 .9375em;
    &__items{
      max-height: 27em;
    }
  }

  .search-field{
    input{
      height: 2.8125em;
    }
  }

  .search-item{
    grid-template-columns: 5.5em calc(100% - 5.5em - .9375em);
    &__btn{
      grid-column: 1/-1;
      height: 2.125em;
      span{
        font-size: .8125em;
      }
    }
  }

  .empty-search{
    padding: .625em .9375em;
  }
}


@media screen and (max-width: 576px) {
  .header{
    padding-bottom: 3.94em;
    &__call{
      &--hide-mob{
        display: none;
      }
    }
    &__btn{
      height: 2.3125em;
    }
  }

  .header-top {
    padding: .625em 0;
  }

  .header-contacts{
    margin-right: 0;
    &__hidden{
      min-width: unset;
      width: calc(100vw - 1.875em);
    }
    .header__call{
      display: flex;
      width: 100%;
      height: 2.9375em;
      border-radius: 0;
      font-weight: 700;
      font-family: $font1;
    }
  }

  .header-search{
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 0 .9375em .9375em;
    width: 100%;
    margin: 0;
  }

  .m-search{
    &__result{
      margin-top: -.9375em;
      left: .9375em;
      width: calc(100% - 1.875em);
    }
  }

  .search-field{
    input{
      height: 3em;
    }
  }
}
