.login{
  padding-bottom: 6.25em;
  &__inner{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
    margin-top: 2.5em;
  }
}

.login-form{
  padding: 2.125em;
  border: .6875em solid $lightGray;
  @include grid100gap(1.56em);
  &__title{
    text-align: center;
  }
  &__fields{
    @include grid100gap(1.125em);
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__link{
    margin-top: -.375em;
  }
  &__desc{
    text-align: center;
    margin-top: -.5em;
  }
  .empty-catalog{
    margin-top: -.25em;
    justify-content: unset;
  }
}

.login-info{
  height: 100%;
  padding: 2.81em;
  background-color: $lightGray;
  display: flex;
  &__icon{
    width: 3.375em;
    flex-shrink: 0;
    margin-right: .9375em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__top{
    min-height: 3.375em;
    display: flex;
    align-items: center;
  }
  .text{
    margin-top: 1.56em;
    color: $black2;
    font-weight: 400;
    p{
      @include lastM(0 0 .5em);
    }
  }
  &__nav{
    display: flex;
    margin-top: 1.56em;
    .m-btn{
      padding: 0 2.19em;
      img{
        margin-left: 1.562em;
      }
    }
  }
}

.reg-form{
  .login-form__btn{
    width: 100%;
    padding: 0;
  }
  &__checks{
    margin-top: -.375em;
    @include grid100gap(1.25em);
  }
}

.reg-info{
  height: unset;
  position: sticky;
  top: 1em;
}

.pass-recover{
  .text58{
    text-align: center;
  }
  .login-form{
    margin: 2.5em auto 0;
    max-width: 40.625em;
  }
  .login-form__btn{
    width: 100%;
    padding: 0;
  }
}


@media screen and (max-width: 992px){
  .login{
    padding-bottom: 5em;
    &__inner{
      grid-template-columns: repeat(2,calc(50% - .9375em));
      gap: 1.875em;
      margin-top: 1.875em;
    }
  }

  .login-form{
    border-width: .4375em;
    padding: 1.125em;
    &__nav{
      flex-direction: column;
      .m-check{
        margin: .625em 0 0;
      }
    }
    &__btn{
      padding: 0 !important;
      width: 100%;
    }
  }

  .login-info{
    padding: 1.56em;
    align-items: center;
    flex-direction: column;
    text-align: center;
    &__icon{
      margin: 0 0 .9375em;
    }
    &__content{
      width: 100%;
    }
    &__nav{
      width: 100%;
      .m-btn{
        padding: 0;
        width: 100%;
      }
    }
    &__top{
      min-height: unset;
      justify-content: center;
    }
  }

  .reg{
    .login__inner{
      grid-template-columns: 100%;
    }
  }

  .pass-recover{
    .login-form{
      margin-top: 1.875em;
      max-width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .login{
    &__inner{
      grid-template-columns: 100%;
    }
  }
}
