.error404{
  padding-bottom: 6.25em;
  background-image: url(../img/404-bg.svg);
  background-position: right 6.56em top 2.5em;
  background-repeat: no-repeat;
  background-size: 66.6875em auto;
  &__grid{
    margin-top: 1.125em;
    display: grid;
    grid-template-columns: 44em calc(100% - 49.5em);
    gap: 4.5em;
    align-items: center;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    @include grid100gap(1.56em);
  }
  &__text{
    color: $gray;
  }
  &__nav{
    display: flex;
    .m-btn{
      padding: 0 2.31em;
      img{
        margin-left: 1.56em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .error404{
    padding-bottom: 5em;
    &__grid{
      margin-top: 1.375em;
      grid-template-columns: 20em calc(100% - 23em);
      gap: .9375em 3em;
      align-items: flex-start;
    }
    &__content{
      gap: .9375em;
    }
    .search-field{
      input{
        height: 3.4375em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .error404{
    &__grid{
      margin-top: 1.56em;
      grid-template-columns: 100%;
    }
    &__content{
      text-align: center;
    }
    &__nav{
      justify-content: center;
    }
  }
}
