@import "../vars";
button{
    color: $black;
}

.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    font-family: $font1;
    &:focus {
        box-shadow: none;
    }
    &:disabled{
        background: #80776A;
        color: rgba(#fff,.3);
        pointer-events: none;
        img{
            opacity: .3;
        }
    }
    &._arrow{
        padding: 0 4.375em;
        height: 3.75em;
        text-transform: uppercase;
        font-weight: 500;
        span{
            font-size: 1em;
            line-height: 118%;
        }
        img{
            width: 1.5em;
            flex-shrink: 0;
            margin-left: .5em;
        }
    }
}

.m-btn-white{
    background: #FFF;
    color: $primary;
    &:hover{
        background: $primary;
        color: #fff;
    }
}

.m-btn-black,
.m-btn-primary{
    img{
        transition: inherit;
    }
    &:hover{
        color: rgba(#fff,.7);
        img{
            opacity: .7;
        }
    }
}

.m-btn-black{
    background: $black;
    color: #fff;
    &:hover{
        background: $black3;
    }
    &:disabled{
        color: #FFF;
        background: rgba($black3,.4);
        img{
            opacity: 1;
        }
    }
}

.m-btn-primary{
    background: $primary;
    color: #fff;
    &:hover{
        background: #F36600;
    }
}

.m-btn-primary-outline{
    border: 2px solid $primary;
    color: $primary;
    font-weight: 500;
    text-transform: uppercase;
    border-radius: 0.125em;
    height: 2.3125em;
    padding: 0 1.25em;
    &:hover{
        background: $primary;
        color: #fff;
    }
}

.m-btn-gray-outline{
    border: 2px solid #E9ECEE;
    color: $black3;
    font-weight: 700;
    text-transform: uppercase;
    border-radius: 0.125em;
    height: 2.3125em;
    padding: 0 1.25em;
    &:hover{
        background: $black;
        border-color: $black;
        color: #fff;
    }
    span{
        font-size: .9375em;
    }
}
